<template >
    <div class="mySelect" onresize="alert('You have changed the size of the window')" ref="selectDiv" :style="{width:  option.width || '14em', position: option && option.position || 'relative'}" v-if="option">
    <template v-if=" option.type != 'complexSelect' && option.type != 'checkSelect'">
          <button v-if="constant != ''" type="button" class=" selectBtn" :value="constant[option.value]" @click="showOption" tabindex="1" @blur="hideOption" >
        <span class="selText">{{option.prefix || '' }}  {{ constant[option.text || 'text'] }}</span>
        <i class="hasIcon special" :style="{marginTop: option && option.orgin === 'top' ? '0.4em' : '-0.4em'}">
          <icon :name="option.orgin === 'top' ?  'sort-asc' : 'sort-desc'" scale="1.3"></icon>
        </i>
      </button>
      <button v-if="constant == ''" type="button" class=" selectBtn">
       暂无数据
      </button>
      <ul v-if="datas != ''" class="selectUl" ref="normalSelectUI" v-show="showSelect" :style="{'width': selectConWidth =='' ? '100%' : selectConWidth,  position: selectConWidth =='' ? 'absolute' : 'fixed'}" :class="option.classes" >
        <li v-for="(item, $index) in datas" :class="{'liSelect': index == $index}" 
            :value="item[option.value || 'value']" @mousedown="selectChange(item, $index)">
          {{item[option.text || 'text']}}
        </li>
      </ul>
    </template>
    <template v-if=" option.type == 'complexSelect'">
      <button v-if="constant != ''" type="button" class="selectBtn" :value="option.MultiSelect ? multiValue : constant[option.value || 'value']"
              @click="showOption" tabindex="1" @blur="!option.MultiSelect && hideOption">
        <span class="selText">
           {{option.MultiSelect ? multiText : constant[option.text] || constant['text']}}
         </span>
        <i class="hasIcon special">
          <icon name="sort-desc" scale="1.3"></icon>
        </i>
      </button>
      <button v-if="constant == ''" type="button" class=" selectBtn">
       暂无数据
      </button>
    <!--   <div class="complexUlContainer" :style="{width: option.width || '14em'}" v-show="showTree"> -->
      <div class="complexUlContainer" :style="{'width': selectConWidth =='' ? '100%' : selectConWidth,  position: selectConWidth =='' ? 'absolute' : 'fixed'}" v-show="showTree" v-if="showComplex" ref="normalSelectUI">
          <ul v-for="(menuItem, $index) in datas" class="sUl" style="padding-left:0"  >

              <myTree :model="menuItem" @getSelectMenu="getSelectMenu" :MultiSelect="option.MultiSelect" :noCheck="option.noCheck" :multiValue="multiValue" :text="option.text" :value="option.value"></myTree>
             <!--  <li class="selliSubmit" v-if="option.MultiSelect" @click="closeComplex" >
                <div>确定</div>
              </li> -->
          </ul>
      </div>
    </template>

    <template v-if="option.type == 'checkSelect'">
      <button type="button" class="selectBtn" :value="checkItem.value" @click="showOption"
              tabindex="1"> <span class="selText">{{ checkItem.text }}</span>
        <i class="hasIcon special" style="">
          <icon name="sort-desc" scale="1.3"></icon>
        </i>
      </button>

      <ul v-if="datas != ''" class="selectUl" v-show="showCheck" :style="{width: '100%'}">
        <li v-for="(item, $index) in datas" :class="{'liSelect':item['checked'] }"
            :value="item[option.value || 'value']" @mousedown="selComChange(item)" >
          <span v-show="item['checked']">✔</span><label>{{item[option.text || 'text']}}</label>
        </li>
        <li class="selliSubmit" @click="comfimSelect" ><div>确定</div></li>
      </ul>
    </template>
    <dialogs type="alert" ref="alerts"></dialogs>
  </div>
</template>
<script>
    import 'vue-awesome/icons/sort-asc'
    import 'vue-awesome/icons/sort-desc'
    import myTree from './recursionUl.vue'
    export default {
        data () {
            return {
                selectConWidth: '',
                flag: true,
                showTree: false,
                treeMenuText: '',
                treeMenuValue: '',
                datas: [],
                constant: {}, //初始化的值。。如果是空。。默认选择第一项
                index: -1,
                showSelect: false,
                search: '',
                showWatch: true,
	              showCheck: false,
	              checkItem:{text:"",value:""},
                multiSelected: [],
                multiText: '',
                multiValue: '',
                showComplex: false,
                
            }
        },
        components: {
            myTree
        },
        props: ['option'],
        methods: {
          closeComplex: function () {
           // if(this.multiSelected.length > 0) {
           //      this.hideOption();
           // } else {
           //   this.$refs.alerts.show({
            //      msg: '选项不能为空'
            //  })
           //} 
            this.$emit('getSelect', {key: this.option.key || '', val:  this.multiValue, text:  this.multiText, currentSelect: this.multiSelected, obj: this.$parent}); 
          },
          getSelectMenu: function (val) {
              if(this.option.MultiSelect) {
                  if(val != '') {
                     this.showTree = true;
                    let len = this.multiSelected.length;
                    if(len == 0) {
                      this.constant = val;
                      this.multiSelected.push(val)
                    } else {
                       for(let i=0; i<len;i++) {
                        let item =  this.multiSelected[i];
                        if(item[this.option.value || 'value'] == val[this.option.value || 'value']) {
                          this.multiSelected.splice(i, 1);
                          break;
                        }
                        else if(i == len - 1) {
                          this.multiSelected.push(val)
                        }
                      }
                    }
                      let texts = [];
                      let values = [];

                      for(let j=0; j<this.multiSelected.length; j++) {
                         let items =  this.multiSelected[j];
                         texts.push(items[this.option.text || 'text']);
                         values.push(items[this.option.value || 'value'])
                      }
                      this.multiText = texts.join(',');
                      this.multiValue = values.join(',');
                      this.closeComplex();
                  }
                      
                } else {
                 if (val !== '') {
                   let that = this;
                   this.constant = {};
                   this.$nextTick(function () {
                     that.constant = val;
                   });
                   this.$emit('getSelect', {key: this.option.key || '', val: val[this.option.value || 'value'], text: val[this.option.text || 'text'], currentSelect: this.constant, obj: this.$parent});
                   this.showTree = false;
                   this.flag = true;
                   } else {
                    this.flag = false;
                  }
                }
          },
          resetConstant: function (constant) {
            if (this.datas.length > 0) {
              if (constant !== '') {
                if (constant[this.option.text]) {
                  this.constant = constant;
                } else {
                  if(this.option.type !== 'complexSelect') {
                   for (let i=0; i<this.datas.length; i++) {
                    if (this.datas[i][this.option.value || 'value'] == constant[this.option.value || 'value']) {
                      this.constant = this.datas[i];
                      break
                    }
                   }
                  } else {

                    let key = constant[this.option.value || 'value'];
                    if(typeof key !== 'undefined' && key != 'undefined') {
                       this.getConstantName(this.datas, constant[this.option.value || 'value'])
                       } else {
                         this.getConstantName(this.datas)
                       }
                   
                  }
                }
              } else {
                this.constant = this.datas[0]
              }
              this.$emit('getSelect', {key: this.option.key || '', val: this.constant[this.option.value || 'value'], text: this.constant[this.option.text || 'text'], currentSelect: this.constant, obj: this.$parent});

            }
          },
          initSelect: function () {
                this.datas = [];
                this.constant = '';
                let options = this.option;
                options['value'] = this.option.value || 'value';
                options['text'] = this.option.text || 'text';
               if (options.cs === 'client' && options.data && options.data.length > 0) {
                   let Data = options.data;

                   if (Data[0] && typeof Data[0] === 'object') {
                       this.datas = Data;
                   } else {
                       for (let j=0; j<Data.length; j++) {
                           this.datas.push({text: Data[j], value: Data[j]})
                       }
                   }
                  if (this.option.type != 'checkSelect') {
                    //单选
                    if (options.constant  && typeof options.constant[options.value] !== 'undefined' && options.constant[options.value] !== null) {
                        for (let i=0; i<this.datas.length; i++) {
                            if (this.datas[i][options.value] == options.constant[options.value]) {
                                this.constant = this.datas[i];
                                break
                            }
                            if (i === this.datas.length-1 && options.constant[options.text] !== '') {
                                this.constant = options.constant;
                            }
                        }
                    } else {
                        this.constant = this.datas[0];
                    }
                    this.$emit('getSelect', {
                     key: this.option.key || '',
                     val: this.constant[this.option.value || 'value'],
                     text: this.constant[this.option.text || 'text'],
                     currentSelect: this.constant,
                     obj: this.$parent
                     });
                    this.$emit('getSelects', {
                     key: this.option.key || '',
                     val: this.constant[this.option.value || 'value'],
                     text: this.constant[this.option.text || 'text'],
                     currentSelect: this.constant,
                     obj: this.$parent
                     });
                  }
                  else {
	                    //复选
	                     this.getCheckVals();
	                 }
                  this.showComplex = true;
                } else if (options.url){
                    let that = this;
                    that.getRequest({
                        method: options.method || 'get',
                        url: options.url,
                        param: options.param,
                        success: function (data) {
                            let row = that.option.rowName || 'data';
                            if (data[row].length > 0) {
                                let Data = data[row];
                                if (Data[0] && typeof Data[0] === 'object') {
                                    that.datas = data[row];
                                } else {
                                    for (let i=0; i<Data.length; i++) {
                                        that.datas.push({text: Data[i], value: Data[i]})
                                    }
                                }
                                if(that.option.hasAll) {
                                   let fistItem = {};
                                   fistItem[that.option.text || 'text'] =  '全部';
                                   fistItem[that.option.value || 'value'] =  '';
                                   that.datas.unshift(fistItem)
                                }
                                //options.constant 默认值
                                if (options.constant  && options.constant !== '' && typeof options.constant[options.value || 'value'] !== 'undefined' && options.constant[options.value || 'value'] !== null && options.type !== 'complexSelect' ) {

                                    if (options.constant[options.text || 'text'] && options.constant[options.text || 'text'] !== '') {
                                      that.constant = options.constant;
                                    } else {
                                      for (let i=0; i<that.datas.length; i++) {
                                        if (that.datas[i][options.value || 'value'] == options.constant[options.value || 'value']) {
                                          that.constant = that.datas[i];
                                          break
                                        }
                                        if (i ===that.datas.length-1 && options.constant[options.text || 'text'] !== '') {
                                          that.constant = options.constant;
                                        }
                                      }
                                    }

                                } else if (options.constant  && typeof options.constant[options.value || 'value'] !== 'undefined' && options.constant[options.value || 'value'] !== null && options.type === 'complexSelect' ) {
                                   if(that.option.MultiSelect) {
                                       that.multiText = options.constant[that.option.text || 'text'];
                                       that.multiValue = options.constant[that.option.value || 'value'];
                                       that.constant = options.constant;
                                       let arr1 = options.constant[that.option.value || 'value'].split(',');
                                       let arr2 = options.constant[that.option.text || 'text'].split(',');
                                       that.multiSelected = [];
                                       for(let m=0; m<arr1.length;m++) {
                                         let item = {};
                                         item[that.option.value || 'value'] = arr1[m];
                                         item[that.option.text || 'text'] = arr2[m];
                                        
                                         that.multiSelected.push(item);
                                       }
                                      
                                    } else {
                                      if(options.constant[options.text || 'text']) {
                                         that.constant = options.constant;
                                      } else {
                                        let key = options.constant[options.value || 'value'];
                                        if(key !== '') {
                                         that.constant= {};
                                         that.constant[options.value || 'value'] = key;
                                         that.getConstantName(data[row], key)
                                       }
                                     
                                      }
                                    }
                                  } else {

                                   if(that.option.MultiSelect) {
                                       that.multiText = that.datas[0][that.option.text || 'text'];
                                       that.multiValue = that.datas[0][that.option.value || 'value'];
                                       that.multiSelected = [];
                                       that.multiSelected.push(that.datas[0]);
                                    } 
                                   that.option.noCheck ?   that.getConstantName(data[row]) :  that.constant = that.datas[0];
                                }

                                that.$emit('getSelect', {key: that.option.key || '', val: that.option.MultiSelect ? that.multiValue : that.constant[that.option.value || 'value'], text: that.option.MultiSelect ? that.multiText : that.constant[that.option.text || 'text'], currentSelect: that.option.MultiSelect ? that.multiSelected : that.constant, obj: that.$parent});
                                that.$emit('getSelects', {key: that.option.key|| '', val:  that.option.MultiSelect ? that.multiValue : that.constant[that.option.value || 'value'], text: that.option.MultiSelect ? that.multiText : that.constant[that.option.text || 'text'], currentSelect: that.option.MultiSelect ? that.multiSelected :  that.constant, obj: that.$parent});
                                that.showComplex = true;
                            } else {
                               that.constant = '';
                               if(that.option.hasAll) {
                                   let fistItem = {};
                                   fistItem[that.option.text || 'text'] =  '全部';
                                   fistItem[that.option.value || 'value'] =  '';
                                   that.datas.unshift(fistItem);
                                   that.constant = that.datas[0];
                                }

                                that.$emit('getSelect', {key: that.option.key || '', val: that.constant[that.option.value || 'value'] || '', text: that.constant[that.option.text || 'text'] || '', currentSelect: that.constant, obj: that.$parent});

                            }

                        }
                    }, true);

                }
            },
          getConstantName: function (row, key) {
            for (let i=0; i<row.length; i++) {
             
              if(((key && row[i][this.option.value || 'value'] == key) || typeof key == 'undefined' ) && (typeof this.option.noCheck != 'undefined' && row[i][this.option.noCheck.key] != this.option.noCheck.value) ||typeof this.option.noCheck == 'undefined') {
                 this.constant = row[i];
                 return;
              } else if(key && row[i][this.option.value || 'value'] == key && typeof this.option.noCheck != 'undefined' &&  row[i][this.option.noCheck.key] == this.option.noCheck.value) {
                 this.getConstantName(row);
              } else {
                if(row[i].children && row[i].children.length > 0) {
                   typeof key != 'undefined' ? this.getConstantName(row[i].children, key) :  this.getConstantName(row[i].children)
                }
              }
            }
          },  
          selectChange: function (val, index) {
              this.constant = val;
              this.index = index;
              this.$emit('getSelect', {key: this.option.key || '', val: val[this.option.value || 'value'], text: val[this.option.text || 'text'], currentSelect: this.constant, obj: this.$parent});
              this.showSelect = false
          },
          resetDefault: function (val,datas) {
              let rsData=datas?datas:this.datas;
              for (let i=0; i< rsData.length; i++) {
                let item = rsData[i];
                if (item[this.option.value] === val) {
                  this.constant = item;
                  break
                }
                else if (item.children) {
                  this.resetDefault(val, item.children)
                }
              }

            if (val == 0) {
              let tempItem = {};
              tempItem[this.option.value || 'value'] = val;
              tempItem[this.option.text || 'text'] = '最顶级';
              this.constant=tempItem;
            }
          },
          showOption: function () {
              this.resetPosition();
              this.showSelect = !this.showSelect;
	            this.showTree = !this.showTree;
	            this.showCheck = !this.showCheck;
          },
          resetPosition: function () {
          	  if(this.option.classes !== 'selectUlTop') {
          	  	 let that = this;
                 let ch = that.$refs.normalSelectUI;
                 let fa = that.$refs.selectDiv;
                 if(ch) {
                   let faPosition = fa.getBoundingClientRect();

                 let f = fa.clientWidth;
                 let c = ch.clientWidth;
                 that.selectConWidth = (c > f ? c : f) + 'px';
                 ch.style.left = faPosition.left + 'px';
                 ch.style.top = (fa.clientHeight + faPosition.top) + 'px'
               }
          	  }
             
        },
     	  selComChange: function (item) {
	        if (item.checked) {
	          item.checked = false;
	        }
	        else {
	          item.checked = true;
	        }
	        this.getCheckVals();
	      },
          hideOption: function () {
              this.showSelect  = false;
              if (this.flag) {
                  this.showTree = false;
              }
              this.showCheck = false;
          },
          resetOption: function (opt) {
              for(let item in opt){
                  this.option[item] = opt[item]
              }
              this.initSelect();
	      },
	      //获取选择的项目
	      getCheckVals:function(){
	        let selText = "";
	        let selVal = "";
	        for (let i = 0; i < this.datas.length; i++) {
	          let item = this.datas[i];
	          if (item.checked) {
	            selText= selText+ ","+item.text;
	            selVal =selVal+","+item.value;
	          }
	        }
	        this.checkItem.text = selText.replace(/^,/,"");
	        this.checkItem.value = selVal.replace(/^,/,"");

	        this.$emit('getSelect', {
	          key: this.option.key || '',
	          val: this.checkItem.value == "" ? [] : this.checkItem.value.split(','),
	          text:this.checkItem.text,
	          obj: this.$parent
	        });
	      },
	      //确定选择
	      comfimSelect:function(){
	        this.$emit('comfim');
	        this.hideOption();
	       },
         judgeSameObj: function(newObj, oldObj) {
            let oldStr = '';
            let newStr = '';
            for(let key1 in oldObj) {
               oldStr = oldStr + oldObj[key1]
            }
            for(let key2 in newObj) {
               newStr = newStr + newObj[key2]
            }
            if(oldStr === newStr) {
              return true
            }
            return false
         },
         windowChange: function () {
          // this.showSelect = this.showComplex = false;
           this.resetPosition()
         }
        },
        mounted: function () {
          let that = this;
          if(this.option) {
              this.initSelect();
          }
        },
        watch: {
          'option.constant': {
                handler(val, oldVal) {
                    if (!this.option.noWatch) {

                        this.resetConstant(val)
                    }
                }
          },
          'option.param': {
               handler(val, oldVal) {
                   
                    if (!this.option.noWatch && !this.judgeSameObj(val, oldVal)) {
                        this.index = -1;
                        this.initSelect();
                        this.option.constant = '';
                       // this.multiText = '';
                        this.multiSelected = []
                    }
                }
          },
          'option.data': {
                handler(val, oldVal) {
                    if (this.option.cs !== 'server' && !this.option.noWatch) {
                        this.index = -1;
                        this.initSelect();
                    }
                }
          }
        }
    }
</script>
<style lang="scss">
    @import "../../assets/main.scss";
    .mySelect {
        float:left;
        position:relative;
        width:200px;
        -webkit-user-select: none;
        -moz-user-select: none;
        user-select: none;
    }
    .mySelect:after{
       @extend .clearElement
    }
   .selectUl{
     //  width:100%;
       max-height: 200px;
       overflow-y: auto;
       overflow-x: hidden;
     //  border:1px solid lightgray;
       border-bottom-left-radius: 4px;
       border-bottom-right-radius: 4px;
       position:absolute;
       z-index:2;
      // background:white;
        font-size: 0.83em;
        color:white;
        background: #305ea0;
        border:1px solid rgba(255,255,255,0.3);
   }
    .selectUl:after{
        content:'';
        width:1px;
        height:0;
        clear:both
    }

	  .selectUl li:not(.selliSubmit) {
	    min-width: calc(100% - 2em) !important;
	    padding: 0.5em 1em !important;
	    margin: 0.5px 0 !important;
     // min-width: fit-content!important;
	    cursor: pointer;
	    white-space: nowrap;
	    text-overflow: ellipsis;
	    overflow: hidden;
	    text-align: left !important;
	    float: left;
	  //  color: #4D4D4D;
      color:white;
	    line-height: 1.2;
	    position: relative;
	    height: 1.2em;
	    p{
	      margin:0
	    }
	    label{
	      position: absolute;
	      left: 2.1em;
	    }
	    span{

	    }
	  }

	  .selectUl li:hover:not(.selliSubmit), .selectUl li.liSelect {
	    //background: #4C5E76;
	    color: white;
      background: #227fdf;
	  }
    
	 .selectUl .selliSubmit{
	    width: calc(100% - 0.4em) !important;
	    padding: 2px  3px!important;
	    margin: 0.5px 0 !important;
	    float: left;
	  }
	  .selliSubmit div{
	    width: 100%;
	    height:2em;
	    line-height: 2em;
	    cursor: pointer;
	    text-align: center !important;
	    background: #227fdf ;
	    color: white !important;
      margin: 0.2em 0;
	  }
	  .selliSubmit div:hover{
	    background: #27A5FF;
	  }
    .selectBtn{
        width:100%;
        position:relative;
     //   background:transparent;
        text-align:left;
        outline: none!important;
        border:1.5px solid;
      //  background: #4d5f77;
        color:white;
        border-radius: 0.2em;
        border-color:#227fdf;
        background: rgba(#227fdf, 0.2);
        line-height: 1.3!important;
        left: 0;
    }
    .selText{
	    right: 1.8em;
	    position: absolute;
	    top: 29%;
	    width: calc(100% - 2.6em);
	    overflow: hidden;
	    text-overflow: ellipsis;
	    display: block;
	    -webkit-line-clamp: 2;
	    -webkit-box-orient: vertical;
	    white-space: nowrap;
	    padding-left: 0.8em;
	    line-height: 1.23!important;
    }
    .selectBtn .hasIcon{
      color:white;
      margin-top: -0.4em;
      margin-right: 0;
    }

    .complexUlContainer {
        border: 1px solid lightgray;
       // background: white;
        color:white;
        background: #305ea0;
        border:1px solid rgba(255,255,255,0.3);
        position: absolute;
        z-index: 100;
        min-width: 12em;
        height: max-content;
        height:  -webkit-max-content;
        height:  -moz-max-content;
        height: max-content;
        overflow: auto
    }
    .myTimeSelect {
      float:right;
      margin-right:2.2em;
      font-size: 0.875em;
      button{
        height: 3.292em!important;
      }
    }
  .info-select{
    .selectBtn{
      height: 3.129em;
      line-height:1.5!important;
    }
  }
  .selectUlTop{
      bottom:82%;
      left:-1em;
      border-top-left-radius: 4px;
      border-top-right-radius: 4px;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
  }
</style>



