<template>
    <!--父组件中如下使用type为customer类型的模态框，所有的事件和数据来源都在父组件实现和读取
            <Dialog type="customer" ref="testDialog">
            <span slot="header">测试标题</span>
            <div slot="content">
                <Table :tableOpt="tableOpt"></Table>
            </div>
            <div slot="footer">
                <input type="text" v-model="texts" placeholder="这里输入的内容将通过按钮点击事件读取"/>
                <button @click="test">测试按钮</button>
            </div>
        </Dialog>
    -->
    <div class="myDialogs" v-show="showDialog" :class="{'loadingIndex':type === 'loading' || type === 'progressBar', 'alertIndex': type === 'alert',  'confirmIndex': type === 'confirm','formIndex': type === 'customer', 'senFormIndex': top }">
        <div class="dialogWrapper"  ref="myDialog" >
            <div class="dialogContent" :style="{width: width, minWidth: minWidth, 'padding-bottom': isPadding ? '1em' : 'auto'}">
                <div class="dialogHeader" :class="{customerHeader: type === 'customer'}"
                     :style="{cursor: cursor}"
                     >
                    <span v-if="type !== 'customer'">{{title}}</span>
                    <slot name="header"></slot>
                    <i  @click="close(1)"  @mouseover="changeRemoveImg" @mouseout="changeRemoveImg" class="hasIcon hasImg"  v-if="type === 'alert'|| (type === 'loading'&& option.hasClose)" style="float: right;margin-right: -0.4em;cursor:pointer">
                      <!--<icon v-if="type === 'alert'" name="remove" class="icon" scale="1.3"></icon>-->
                      <img v-if="!changeRemove"  src="../../assets/img/close-icon.png" style="width: 1.6em;height: 1.6em;"/>
                      <img v-if="changeRemove" src="../../assets/img/close-active-icon.png" style="width: 1.6em;height: 1.6em;"/>
                    </i>
                    <i  @click="close(1)"  @mouseover="changeRemoveImg" @mouseout="changeRemoveImg" class="hasIcon hasImg"  v-if="(type === 'customer') && option.hasClose" style="float: right;margin-right: -0.4em;cursor:pointer;margin-top: -1.3em;">
                    <!--<icon v-if="type === 'alert'" name="remove" class="icon" scale="1.3"></icon>-->
                      <img  v-if="!changeRemove" src="../../assets/img/close-icon.png" style="width: 1.6em;height: 1.6em;" />
                      <img  v-if="changeRemove" src="../../assets/img/close-active-icon.png" style="width: 1.6em;height: 1.6em;" />
                   </i>
                   <div class="bottom-border"></div>
                </div>
                <VuePerfectScrollbar  v-if="overflow" class="dialogMain" :class="{customerContent: type === 'customer'}" :style="{overflow: overflow,  height: height,'max-height': maxHeight + '!important' ||  'auto', padding: type !== 'customer' && type !== 'confirm'? '0.5em 2em 3em' : 'auto'}">
                    <template v-if="type !== 'customer'">
                        <img v-if="type == 'loading' || type == 'progressBar'" class="loadingImg"  src="dist/assets/img/table/loading.gif">
                        <div v-if="option.msg !== ''" class="loadMsg" >
                            <p v-html="option.msg"></p>
                        </div>
                       <div class="loadMsg"  v-if="type == 'progressBar'">
                         <p v-html="progress !== '' ? progress : option.progress"></p>
                       </div>
                    </template>
                    <template  v-if="type == 'customer'" >
                        <slot name="content" ></slot>
                    </template>
                </VuePerfectScrollbar>
                <div v-else class="dialogMain" :class="{customerContent: type === 'customer', customerContentNoB: hasNoPaddB}" :style="{overflow: overflow,  height: height,'max-height': maxHeight + '!important' ||  'auto', padding: type !== 'customer' && type !== 'confirm'? '0.5em 2em 3em' : 'auto'}">
                <template v-if="type !== 'customer'">
                  <img v-if="type == 'loading' || type == 'progressBar'" class="loadingImg"  src="dist/img/loading.gif">
                  <div v-if="option.msg !== ''" class="loadMsg" >
                    <p v-html="option.msg"></p>
                  </div>
                  <div class="loadMsg"  v-if="type == 'progressBar'">
                    <p v-html="progress !== '' ? progress : option.progress"></p>
                  </div>
                </template>
                <template  v-if="type == 'customer'">
                  <slot name="content"></slot>
                </template>
              </div>
                <div class="dialogFooter" :style="{background: type !== 'alert' && type !== 'confirm' ?   'rgba(0,0,0,0)' : 'transparent'}">
                    <template v-if="type!== 'customer' " >
                        <button class="button-default" :class="{'button-confirm': type === 'confirm'}" v-if="option.isShow || this.option.btnMsg || type === 'confirm'" style="width: 122px" @click="confirmSure">{{btnMsg}}</button>
                        <button class="button-default cancel-button" :class="{'button-confirm': type === 'confirm'}"  v-if="type !== 'progressBar' && type !== 'loading' && type !== 'alert'" style="width: 122px" @click="close(1)">{{option.btnCancel||'取消'}}</button>
                    </template>
                    <slot name="footer"></slot>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
       export default {
      data() {
          return {
              changeRemove: false,
              showDialog: false,
              startX: '',
              startY: '',
              cursor: 'default',
              moveFlag: false,
              targetNow: '',
              progress: ''
          }
      },
      /*
      *   option: 对象 {
      *   msg：展示的信息；
      *   action: {
      *     cancel: {
       *      msg: '取消按钮的显示文本，默认为取消',
       *      callback：取消之后的操作
       *    },
       *   confirm： {
       *      msg: '确认按钮的显示文本，默认为确定',
       *      callback：确认之后的操作
       *    },
       *    .....无限添加操作。。。格式如上
      *   }
      *
      *   }
      * */
      props:
          {
              option: {
                  type: Object,
                  default: function () {
                      return {}
                  }
              },
              hasNoPaddB: {
                type: Boolean,
                default: false
              },
              type: {
                  type: String,
                  required: true
              },
              width: {
                  type: String,
                  default: '30em'
              },
              overflow: {
                  type: String,
                  default: ''
              },
              height:{
                type: String,
              },
              maxHeight: {
                type: String
              },
              minWidth: {
                type: String
              },
             isPadding: {
                  type: String
             },
             top: {
               type: Boolean,
               default: false
             }
          },
      methods: {
          changeRemoveImg: function () {
              this.changeRemove = !this.changeRemove
          },
          mouseUp: function(event) {
              if(event.releaseCapture) {
                  event.releaseCapture();
              }
              else if(window.captureEvents) {
                  window.captureEvents(Event.MOUSEMOVE|Event.MOUSEUP);
              }
              this.cursor = 'default';
              this.moveFlag = false;
              this.targetNow = '';
              document.onmousemove = null;
              document.onmouseup = null;
          },
          mouseMove: function (event) {
              let events  = event || window.event;
              let width = document.body.offsetWidth - this.targetNow.offsetWidth ;
              let moveX = event.x - this.startX;
              let moveY = event.y - this.startY;
              let y = document.body.offsetHeight/2  - moveY;
              if (this.moveFlag && moveX <= width  && moveX >= 0 && moveY >=0 ) {
                  this.targetNow.parentNode.style.marginLeft =  moveX + 'px';
                  this.targetNow.parentNode.style.marginTop  =  - y  + 'px';
                  this.startX = events.clientX - this.targetNow.offsetLeft;
               //   this.startY = events.clientY - this.targetNow.offsetTop;
              }
          },
          mouseDown: function (event) {
              this.targetNow = event.target.parentNode;
              this.startX = event.clientX - this.targetNow.offsetLeft;
              this.startY = event.clientY - this.targetNow.offsetTop;
              this.cursor = 'move';
              this.moveFlag = true;
              if (event.setCapture)
                  event.setCapture();
              else if (window.captureEvents)
                  window.captureEvents(Event.MOUSEMOVE | Event.MOUSEUP);
              let that = this;
              document.onmousemove = function (ev) {
                  that.mouseMove(ev)
              };
              document.onmouseup = function (ev) {
                  that.mouseUp(ev)
              }
          },
          show(option) {
              if (option) {
                  for (let item in option) {
                      this.option[item] = option[item];
                  }
              }
              if (this.type === 'alert') {
                  this.option.msg = `  <i class="hasIcons"><svg version="1.1" role="presentation" width="13.714285714285714" height="16" viewBox="0 0 1536 1792" class="fa-icon">
                            <path d="M768 128q209 0 385.5 103t279.5 279.5 103 385.5-103 385.5-279.5 279.5-385.5 103-385.5-103-279.5-279.5-103-385.5 103-385.5 279.5-279.5 385.5-103zM896 1375v-190q0-14-9-23.5t-22-9.5h-192q-13 0-23 10t-10 23v190q0 13 10 23t23 10h192q13 0 22-9.5t9-23.5zM894 1031l18-621q0-12-10-18-10-8-24-8h-220q-14 0-24 8-10 6-10 18l17 621q0 10 10 17.5t24 7.5h185q14 0 23.5-7.5t10.5-17.5z"></path> 
                          </svg></i>` + this.option.msg
              } else if(this.type === 'confirm') {
                this.option.msg = ` <i class="hasIcons"><svg version="1.1" role="presentation" width="13.714285714285714" height="16" viewBox="0 0 1536 1792" class="fa-icon">
                          <path d="M896 1376v-192q0-14-9-23t-23-9h-192q-14 0-23 9t-9 23v192q0 14 9 23t23 9h192q14 0 23-9t9-23zM1152 704q0-88-55.5-163t-138.5-116-170-41q-243 0-371 213-15 24 8 42l132 100q7 6 19 6 16 0 25-12 53-68 86-92 34-24 86-24 48 0 85.5 26t37.5 59q0 38-20 61t-68 45q-63 28-115.5 86.5t-52.5 125.5v36q0 14 9 23t23 9h192q14 0 23-9t9-23q0-19 21.5-49.5t54.5-49.5q32-18 49-28.5t46-35 44.5-48 28-60.5 12.5-81zM1536 896q0 209-103 385.5t-279.5 279.5-385.5 103-385.5-103-279.5-279.5-103-385.5 103-385.5 279.5-279.5 385.5-103 385.5 103 279.5 279.5 103 385.5z"></path> 
                      </svg></i>` + this.option.msg
              }
              this.showDialog = true;
          },
          close(isFunc) {
              //isFunc 是否执行关闭后的函数 1 执行，0不执行
              if (isFunc>0 && this.option.action && this.option.action.cancel) {
                  this.option.action.cancel()
              }
              this.progress = '';
              this.showDialog = false;
          },
          confirmSure() {
              if (this.option.action && this.option.action.confirm) {
                  this.showDialog = false;//关闭窗口
                  this.option.action.confirm()
              }
          },
          changeMsg(msg) {
            this.progress = msg;
          }
      },
      mounted: function () {
          let that = this;
          if (this.type === 'alert') {
              setTimeout(function () {
                  that.close();
              }, 1000)
          }
      },
      computed: {
        //消息标题
        title: function () {
          return this.option.title || '系统提示'
        },
        //确认按钮的名称
        btnMsg: function () {
          return this.option.btnMsg || '确定'
        },
//        //取消按钮的名称
//        btnCancel: function () {
//          return this.option.btnCancel || '关闭'
//        },
      }
    }
</script>
<style lang="scss">
    @import "../../assets/main.scss";
    .alertIndex {
        z-index: 130!important;
    }
    .confirmIndex {
        z-index: 120!important;
    }
    .loadingIndex {
        z-index: 110!important;
    }
    .formIndex {
        z-index: 20!important;
    }
    .senFormIndex {
        z-index: 22!important;
    }
    .myDialogs{
        font-size: $fontSize;
       // background: rgba(0,0,0,0.4);
       background: rgba(0, 0, 0, 0.4);
        z-index:12;
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        display: table;
        .dialogWrapper{
            display: table-cell;
            vertical-align: middle;
            .dialogContent{
                margin: 0 auto;
          //      background-color: #2F3C4F;
           //     border:2px solid #17263D;
                border-radius: 0.4em;
                box-shadow: 0 0.15em 0.45em rgba(0, 0, 0, .33);
             //   background: #15599b;
                background: rgba(#021645, 0.9);
                box-shadow: 0 0 15px #2078c2;
                border:2px solid #2078c2;;
                min-width: 20em;
                .halfLi{
                    width: 50%;
                    float:left;
                    margin-top: 0
                }
                .dialogHeader{
                    -moz-user-select: none;
                    -webkit-user-select: none;
                    -ms-user-select: none;
                    text-align: left;
                    font-size: 0.875em;
                  //  background: url("../../assets/img/title-backgroound.png") no-repeat center ;
                    background-size: contain;
                    color:white;
                    padding: 0.875em;
                    position: relative;
                    border-top-left-radius: 0.4em;
                    border-top-right-radius: 0.4em;
                    background-position: center -0.13em;
                    .icon{
                        position:absolute;
                       right: 0.5em;
                        top: 0.7em;
                        cursor:pointer;
                      //  color: #aaa;
                    }
                    .bottom-border{
                      width:100%;
                      height:2px;
                      background: linear-gradient(to right,#227fdf, rgba(#227fdf, 0));
                      margin-top: 1em;
                    }
                }
                .customerHeader{
                      padding: 1em 1.8em 0;

                 // background: #277CD6;
                 //   background: url("../../assets/img/title-backgroound.png") no-repeat center ;
           //        background: #02615B;
                    text-align:left;
                    box-shadow: 0 0 0 ;
                    .headerTitle{
                      padding:0.33em 2em;
                      background: #02615B;
                      color: white;
                      display: block;
                      margin: 0 calc(50% - 140px) 0 calc(50% - 140px);
                      border-bottom-left-radius: 5px;
                      border-bottom-right-radius: 5px;
                    }
                  .icon{
                    position:absolute;
                    right: 1em;
                    top: 0.7em;
                    cursor:pointer;
                  }
                }
                .dialogMain {
                    padding: 1em 2em;
                    font-size: 0.94em;
                    .loadMsg{
                       color: rgba(255,255,255,0.9);
                        margin: auto;
                        text-align: center;
                         font-size: 0.94em;
                    }
                    .loadingImg{
                         width: 3.34em;
                         height: 3.34em;
                     //    margin-left: calc(50% - 1.67em);
                         margin-top: 2em;
                    }
                }
              .customerContent{
           //     background: #03625C;
                color:white;;
                max-height: 31em;
                font-size:0.84em;
              //  overflow: auto;
              }
              .customerContent:after {
                @extend .clearElement
              }
              .customerContentNoB{
                padding-bottom:0
              }
            }

            .dialogFooter {
                    text-align: center;
                    font-size:0.875em;
             }
          .dialogFooter:after{
              @extend .clearElement;
          }
        }
        button,input,textarea{
            border-radius:0.3em!important;
            height: 2.8em ;
            color:white!important;
            i{
              margin-right:1em;
            }
        }
        input,textarea{
          // background-color: #4d5f77;
          // background: rgba(255, 255,255, 0.1);
          // border-color: #4d5f77
           //border:1.4px solid #17263D!important;
        }
        button.selectBtn{
        //  height: 3.309em;
         // border:1.4px solid #2E917C;
         // background-color: #4d5f77;
          .special{
            margin-top: -0.2em;
            margin-right: 1.5px
          }
        }


    }
    .dialog-ul{
        width:100%;
        li{
            width:100%;
            text-align: center;
             min-width: 15em!important;
            .label-span {
              min-width: 5em;
              font-size: 0.83em;
              text-align: left!important;
            }
        }
        li.formList{
            margin: 0.3em auto;
            .date-button,.time-button {
              line-height: 2!important;
            }
            .date-picker .icon-date{
              margin-right: -0.1em!important;
              margin-top:-0.2em!important
            }
        }
        li:after{
            @extend .clearElement
        }
    }
    .dialog-ul:after{
        @extend .clearElement
    }
  .halfWidth{
    width: calc(50% - 0.5em - 1em)!important;
    margin: 0.2em!important;
    min-width: 18em;
  }
  .hasMarginLi{
    margin:0.2em 0.2em 0.2em 2em!important
  }
  .allWidth{
    width: calc(100% - 0.8em) !important;
    margin: 0.2em 0.2em !important;
  }
    .tableContents {
      max-height: 29em;
      width: 100%;
      .tabDiv{
        max-height: 28em;
        padding-bottom: 1em;
      }
      .tabBtns{
        text-align:left;
        border-bottom: 1px solid lightgray;
        button {
          margin:0;
          border-radius:0;
          outline:none;
        }
      }
      .tabContent {
        padding:0.7em 0 0.4em  0;
        max-height: 26em;
        width: 100%;
      }
      button.special-operate{
        border:0;
        color: #609EE9;
        margin-top:10px;
        float:left;
        background: transparent;
      }
    }
    .tableContents:after{
      @extend .clearElement
    }
  .table-list {
    width:calc(100% - 2.4em);
    margin: 0.3em 1.2em;
    float:left;
    border-top: 1px solid #B6B5B1;
    border-left: 1px solid #B6B5B1;
    table-layout: fixed;
    color: white;
    font-size:1em;
    .table-header {
      background: #15254B;
      th, td{
        padding: 0.6em;
      }
     // background: rgba(53, 136, 250,0.3)
    }
    th,td {
      border-right: 1px solid  white;
      border-bottom: 1px solid  white;
      text-align:center;
      padding: 0.4em  ;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
    .customerContent::-webkit-scrollbar {
       display:none
    }
    .myDialogs {
      .fileinput-remove-button,.fileinput-cancel-button,.fileinput-upload-button{
        height: 34px!important;
        border-radius:0!important;
        color: white!important;
        background:#337ab7!important;
        border-left:0;
        border-top:0;
        border-bottom:0;
        border-right:1.5px solid #ccc;

      }
      a.fileinput-upload-button{
        height:22px !important
      }
    }
    .detail-content {
      padding: 0.5em 2em !important;min-height: 8em;
      .detail-list {
        li {
          margin:0.05em 1em 0.05em 0;
          //min-width: 19em
        }
      }
    }
.hasIcons{
  position: relative;
  top: 0.3em;
  right: 0.5em;
  svg{
    font-size: 1.4em
  }
}
.button-confirm{
  height: 2.7em !important;
  margin: 0.6em 0.6em 1.5em 0.6em!important;
  min-width: 4.5em!important;
  width: 7em!important;
}
    @media screen and (max-width: 1366px) {
      #app {
        .dialogContent .customerContent {
            max-height: 20em;
          }
      }
    }
    @media screen and (min-width: 1367px) and (max-width: 1700px) {
      #app {
        .dialogContent .customerContent {
          max-height: 24em;
        }
      }
    }
    @media screen and (min-width: 1701px) and (max-width: 1920px) {
      #app {
        .dialogContent .customerContent {
          max-height: 34em;
        }
      }
    }
</style>



