import 'es6-promise'
import 'babel-polyfill'
import Vue from 'vue'
import App from './App.vue'
import router from './router/router.js'
import store from './vuex/store.js'
import VueResource from 'vue-resource'
// import 'vue-awesome/icons'
import 'vue-awesome/icons/refresh'
import 'vue-awesome/icons/trash'
import 'vue-awesome/icons/plus'
import 'vue-awesome/icons/search'
import 'vue-awesome/icons/remove'
import 'vue-awesome/icons/save'
import 'vue-awesome/icons/edit'
import 'vue-awesome/icons/download'
import Icon from 'vue-awesome/components/Icon.vue'
import Table from './component/childComponent/Table.vue'
import Dialog from './component/childComponent/Dialog.vue'
import Input from './component/childComponent/Input.vue'
import Select from './component/childComponent/Select.vue'

import DatePicker from './component/childComponent/DatePicker.vue'
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import axios from 'axios'
import Common from './assets/Common.js'

Vue.use(VueResource);
Vue.use(Common);
Vue.prototype.$axios = axios;
Vue.component('icon', Icon);
Vue.component('tables', Table);
Vue.component('dialogs', Dialog);
Vue.component('inputs', Input);
Vue.component('selects', Select);

Vue.component('datePicker', DatePicker);
Vue.component('VuePerfectScrollbar', VuePerfectScrollbar);
Vue.mixin({
  methods: {},
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$nextTick(function() {
        if (typeof vm.windowChange === "function") {
          vm.windowChange();
        }
      })

    });
  },
  watch: {
    '$store.state.windowWidth': {
      handler(val) {
        if (typeof this.windowChange === "function") {
          this.windowChange();
        }
      }
    },
    '$store.state.windowHeight': {
      handler(val) {
        if (typeof this.windowChange === "function") {
          this.windowChange();
        }
      }
    }
  },
  beforeRouteLeave(to, from, next) {
    // this.destroys(from);
    next();
  }
});

new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App),
  created: function() {
    let that = this;
    let configPath = 'dist/pathSet.js';
    that.$axios.get(configPath).then(response  =>  {     
      Vue.prototype.$apiPrefixes  = eval(response.data);
    });
  }
});


