html, body, #app {
  height: 100%;
  width: 100%;
  margin:0;
  padding:0;
  min-height:900px;
  min-width: 1200px;
  overflow-y: auto;
  overflow-x: hidden;
}

body{
  overflow: hidden;
}
#app {
