  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
   background: #17263D;
  color: white;
  position: relative;
}
::-webkit-scrollbar {
 // display:none
}


h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.scroll::-webkit-scrollbar {
  width: 0;
  height: 0.9em;
}

.scroll::-webkit-scrollbar-button {
  /*background-color: #516580;*/
  /*border-radius: 0.2em;*/
}
.scroll::-webkit-scrollbar-button:vertical:start{

}
.scroll::-webkit-scrollbar-button:vertical:end{

}
.scroll::-webkit-scrollbar-track {
  background: #3B4453;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

.scroll::-webkit-scrollbar-track-piece {

}

.scroll::-webkit-scrollbar-thumb {
  border-radius: 0.4em;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background: #516580 url(./assets/img/scroll.png) no-repeat center/ contain;
}

.scroll::-webkit-scrollbar-corner {
  background: #82AFFF;
}
.scroll{
  scrollbar-face-color:#9aa1c4;/*滚动条颜色*/
  scrollbar-highlight-color:#F60;
  scrollbar-3dlight-color:#F00;
  scrollbar-darkshadow-color:#000;
  scrollbar-Shadow-color:#000;
  scrollbar-arrow-color:#394264;/*箭头颜色*/
  scrollbar-track-color:#394264;/*背景颜色*/
}
/* AGENT_SHEET */
/* 应用于所有情况*/
@-moz-document url-prefix(chrome://), url-prefix(about:),
url-prefix(file:///), url-prefix(http://), url-prefix(https://){}

/* 滚动条背景基本样式 */
scrollbar{
  -moz-appearance: none !important;
  background-color: transparent !important;/* 滚动条背景透明 */
  background-image: none !important; /* 滚动条背景图案不显示 */
  position: relative !important; /* 更改滚动条的定位方式为相对 */
  overflow: hidden !important;
  z-index: 999999999 !important; /* 把滚动条提到Z轴最上层 */
}

/* 滚动条按钮基本样式 */
scrollbar thumb{
  -moz-appearance: none !important;
  background-color: rgba(0,100,255,.25) !important;
  border-radius: 0px !important;
  border: 1px !important; /* 滚动条按钮边框 */
  border-color: rgba(0,100,255,.1) !important;  /* 滚动条按钮边框颜色和透明度 */
}

/* 滚动条按钮:鼠标悬停与点击拖动时基本样式 */
scrollbar:hover thumb,
scrollbar thumb:hover,
scrollbar thumb:active {
  background-color: rgba(0,100,255,.75) !important;
  border: 0 !important;
}

/* 垂直滚动条 */
/* 把滚动条位置移到屏幕外，这里的像素应该等于垂直滚动条宽度的负值 */
scrollbar[orient="vertical"]{ margin-left: -5px !important;
