<template>
    <div class="myInput" :style="{width: option.width || '22em'}">
        <div v-if="option.type == 'typeSearch'" class="input-container" :style="{width: '100%'}">
            <input type="text" style="display:none"/>
            <input type="password" style="display:none"/>
            <selects v-show="!option.hideSelect" class="typeSelect" :option="option.selectOpt" @getSelect="getChange" @getSelects="getDefaultSelect" ></selects>
            <input type="text" class="selectInput"
                   :style="{width: option.hideSelect ? (search == '' ? 'calc(100% - 48px - 0px)' : 'calc(100% - 0px)') : (search == '' ? 'calc(100% - '+ (option.selectOpt.width  || '14em') +' / 0.94 - 48px)' :
                    'calc(100% - '+ (option.selectOpt.width  || '14em') +' / 0.94 - 1px)' ),
                   'border-top-left-radius': option.hideSelect ?  '0.2em!important' : '0',
                   'border-top-right-radius': search != '' ?  '0.2em!important' : '0',
                   'border-bottom-right-radius': search != '' ?  '0.2em!important' : '0',
                   'padding-right': '1em',
                   'border-right': search == '' ? '0' :  (option.noBorderSet ? '0' : '1.5px solid  #227fdf !important'),
                   'border-left': !option.hideSelect || option.noBorderSet? '0' : '1.5px solid  #227fdf !important',
                   'border-bottom-left-radius': option.hideSelect ?  '0.2em!important' : '0'}"
                   v-model.trim="search"
                   :maxlength = "option.length || 30"
                   autocomplete="off"
                   @keydown="keyDown($event)" @keyup="keyUp" @blur="searchGo" :placeholder="option.placeholder ||'请输入关键字'" @focus="getFocus($event)"
                   @change="showRemove" :title="holders" />
            <span v-if="!option.hideSelect" class="splitSpan" :style="{left:'calc(' +  option.selectOpt.width + '/ 2 + 5px)'  || '14em'}">  </span>
            <span  v-if="option.inputIcon &&  search === ''" class="inputSpan" :class="option.inputIcon" style="top:10px;color:gray"></span>
            <i  v-show="search !== '' " class=" inputSpan" 
               :style="{right: option.hideSelect ? '1em' : '1em'}"
               style="color:white;" @mousedown="clearInput">
              <icon name="times-circle" scale="1.8"></icon>
            </i>
            <button v-if="option.inputIcon &&  search === ''" class="searchBtn">
              <i class="hasIcon" style="color:white"><icon :name="option.inputIcon "></icon></i>
            </button>
            <button v-if="!option.inputIcon && search === ''" class="searchBtn" @click="searchGo"  >
                <i class="hasIcon" style="color:white;margin-top: -0.15em;font-size:14px;width:0"><icon name="search"></icon></i>
            </button>
        </div>
        <div v-if="option.type == 'normalInput'" class="input-container" :style="{width: '100%'}" >
            <input type="text"  :class="{ 'error-input': showErr}" :style="{width: '100%', color: option.color + '!important' || 'white','border-color': emptyJudge && !isEmpty  ? '#f8a20f' : '#227fdf'}"
                   @focus="getFocus($event)" @keydown="keyDown($event)" :title="option.placeHolder" :placeholder="option.placeHolder" :maxlength = "option.length"
                   v-model.trim="search" @blur="searchGo" @change="showRemove" @keyup="keyUp"  autocomplete="off"  />
            <i  v-show="search !== '' && !(emptyJudge && !isEmpty)" @mousedown="clearInput" class="hasIcon inputSpan special" style="margin-right:0.5em;" :style="{color: showErr ? '#f15649' : 'white'}"><icon scale="1.7" name="times-circle" ></icon></i>
            <i  v-show="emptyJudge && !isEmpty" @mousedown="clearInput" class="hasIcon inputSpan special" style="margin-right:0.5em;color:#f8a20f"><icon scale="1.7" name="exclamation-circle" ></icon></i>
        </div>
        <div v-if="option.type == 'passwordInput'" class="input-container" :style="{width: '100%'}">
            <input type="password" style="display:none" name="pass1" />
            <input type="text" :class="{ 'error-input': showErr }"   autocomplete="off"
                   :maxlength = "option.length || 20"
                   :style="{width: '100%', 'border-color': emptyJudge && !isEmpty  ? '#f8a20f' : '#227fdf'}" @focus="getFocus($event)"  @keydown="keyDown($event)" :title="holders"
                   :placeholder="option.placeHolder" v-model.trim="search" @blur="searchGo"  @keyup="keyUp" @change="showRemove" />
            <i  v-show="removeShow"  @mousedown="clearInput" class="hasIcon inputSpan special" style="margin-right:0.55em;margin-top:0"><icon name="times-circle"></icon></i>
        </div>
        <div v-if="option.type == 'disabledInput'" class="input-container" :style="{width: '100%', background: 'transparent',borderWidth: 0}">
            <input type="text" class=" disabled-input" :style="{cursor: option.cursor || 'not-allowed', width:'100%', background:option.normal ? 'rgba(34, 127, 223, 0.2)' : 'transparent',borderWidth:option.normal ? 1 : 0}" v-model.trim="search"
                   readonly :placeholder="option.placeHolder" />
        </div>
        <!--, 'border-color': emptyJudge && !isEmpty  ? '#f8a20f' : 'transparent'-->
        <div v-if="option.type == 'textArea'" class="input-container" :style="{width: '100%'}">
            <textarea   :maxLength=" option.maxLength || option.length || 200" :style="{width: '100%', height: option.height || '3.74em', whiteSpace : 'normal', overflowY: 'auto', resize: 'none', 'border-color': emptyJudge && !isEmpty  ? '#f8a20f' : '#227fdf'}"
                      :placeholder="option.placeHolder" v-model.trim="search" @blur="searchGo"
                      @keyup= "searchGo" @change="showRemove" autocomplete="off" disableautocomplete></textarea>
        </div>
        <div v-if="option.type == 'combineInput'" class="input-container" :style="{width: '100%'}">
           <input type="text" class="disabled-input combine-input" :style="{width:'calc(100% - 3em - 0px)', background:'transparent',borderWidth:1 }" v-model.trim="search"
                   disabled  />
           <button class="searchBtn inputBtns" @click="editInput" style="width:3em">
                <i class="hasIcon" style="color:white;margin-top: -0.15em"><icon name="edit"></icon></i>
            </button>        
        </div>
    </div>
</template>
<script>
    import 'vue-awesome/icons/times-circle'
    import 'vue-awesome/icons/edit'
    import 'vue-awesome/icons/exclamation-circle'
    import 'vue-awesome/icons/search'
    import selects from './Select.vue'
    export default {
        data () {
            return {
                passwordType: 'text',
                searchType: '',
                showErr: false,
                removeShow: false,
                search: '',
                selects: '',
                holders: '',
                clearData: false,
                emptyJudge: false
            }
        },
        directives: {
          focus: {
            inserted: function (el, {value}) {
              if (value) {
                el.focus();
              }
            }
          }
        },
        props: {
                 'option' : {
                 },
                'isEmpty': {
                     default: true
                }},
        methods: {
          editInput: function () {
             this.$emit('editInput');
          },
            getData: function () {
                let params;
                if (this.option.type === 'typeSearch') {
                    params = {key: this.option.key ? this.option.key + '.' + this.selects.val : this.selects.val, val: this.search, obj: this.$parent, combine: true};
                } else {
                    params = {key: this.option.key, val: this.search, obj: this.$parent}
                }
                return params;
            },
            getControl: function () {
                if (this.clearData && this.option.clear) {
                    this.search = '';
                }
                if (this.search !== '' ){
                    this.removeShow = true;
                }
                this.$emit('onFocus', this.getData());
                this.searchGo();
            },
            keyUp: function () {

                if (this.search !== '' && this.search !== null && typeof this.search !== 'undefined') {
                    let str = this.search.toString();
                    switch (this.option.limit) {
                        case 'EUI':
                            this.search = str.replace(/[^\dA-Za-z]/gi, '').replace(/([\dA-Za-z]{2})((?=\d)|(?=[A-Z])|(?=[a-z]))/g,'$1-');
                            break;
                        case 'charNum':
                        case 'cN':
                            this.search = str.replace(/[^@A-Za-z0-9!%^&*.\-_()]/gi, '');
                            break;
                        case 'NAC':
                            this.search = str.replace(/[^\dA-Za-z]/gi, '');
                            break;
                        case 'NCO':
                            this.search = str.replace(/[^\d-A-Za-z_]/gi, '');
                            break;
                        case 'moteEUI':
                          this.search = str.toString().replace(/[^\dA-Fa-f]/gi, '');
                          break;
                        case 'phone':
                            this.search = str.replace(/[^\d-]/gi, '');
                            break;
                        case 'number':
                            this.search = str.replace(/[^\d]/gi, '');
                            break;
                        case 'float':
                            this.search = str.replace('.','$#$').replace(/\./g,'').replace('$#$','.');
                            //this.search =  parseFloat(src.replace(/[^\d\.]/g,''));
                            break;
                        case 'floatHasTwo':
                            let strs = parseFloat(str.replace(/[^\d\.]/g,''));  
                            let arr = strs.toString().split('.');
                            if(arr.length > 1 && arr[1].length > 2){
                              arr[1] = arr[1].substr(0, 2);
                              this.search = arr.join('.')
                            }  
                             break;
                        case 'upperCase':
                            this.search = str.replace(/[^A-Z]/gi, '').toUpperCase();
                            break;
                        default: 
                            this.search = str.replace(/\s/g, "");
                            break;  
                    }
                    this.$emit('RealData', this.getData())
                } else {
                  this.showErr = false;
                }
            },
            getFocus: function (event) {
                if (this.option.type === 'passwordInput') {
                    event.target.type = 'password'
                }
                if (this.search !== '') {
                    this.removeShow = true;
                }
                this.regularTest()
            },
            clearnInpt:function(){
                this.search="";
            },
            keyDown: function ($event) {
                if($event.code === "Enter" && $event.keyCode === 229) {
                   let that = this;
                   setTimeout(function () {
                     that.keyUp();
                   }, 1)
                }else if ($event.keyCode === 13) {
                  this.searchGo($event);
                }
            },
            regularTest: function () {
              if(this.search !== '' && this.search !== null && typeof this.search !== 'undefined' ) {

              	 if (this.option.regular && (this.option.type === 'normalInput' || this.option.type === 'passwordInput')) {
                if (this.option.regular !== 'zdNo' && this.option.regular !== 'cjqDeviceNo'  && this.option.regular !== 'gwDeviceNo') {
                  let flag = this.regulars[this.option.regular].test(this.search);
                  if (flag) {
                    this.showErr = false;
                  } else {
                    this.holders = this.option.errorMsg;
                    this.showErr = true;
                  }
                } else if (this.option.regular === 'cjqDeviceNo') {
                  let len = this.search.length;
                  if (len >= 13) {
//                    let theNum = this.search.substr(-13, 1);
//                    if (theNum != 8) {
                   //   this.holders = this.option.errorMsg;
                   //   this.showErr = true;
//                    } else {
//                      this.showErr = false;
//                    }
                    this.showErr = false;
                  }  else {
                    this.holders = this.option.errorMsg;
                    this.showErr = true;
                  }
                }else if (this.option.regular === 'gwDeviceNo') {
                  if (this.search.length !== 16) {
                    this.holders = this.option.errorMsg;
                    this.showErr = true;
                  } else {
                    this.showErr = false;
                  }
                } else {
                  if (this.search.length === 9) {
                    let before = parseInt(this.search.substr(0, 4));
                    let last = parseInt(this.search.substr(4));
                    if (before > 0 && last > 0 && before < 10000 && last < 65536) {
                      this.showErr = false
                    } else {
                      this.holders = this.option.errorMsg;
                      this.showErr = true;
                    }
                  } else {
                    this.holders = this.option.errorMsg;
                    this.showErr = true;
                  }
                }
              } else {
                this.showErr = false
              }
              return  this.search
              } else {
                this.showErr = typeof this.search !== 'undefined' ? false : this.showErr;
                return typeof this.search !== 'undefined' ? '' : this.search
              }
             
            },
            searchGo: function (event) {
                this.keyUp();
                this.removeShow = false;
                this.clearData = true;
                let param;
      
                let searches = this.regularTest();

                if (this.option.dataType && searches !== '') {
                    switch (this.option.dataType) {
                        case 'int':
                           this.search = searches = !isNaN(searches) ? parseInt(searches) : '';
                           break;
                        case 'float':
                            this.search = searches = !isNaN(searches) ? parseFloat(searches) : '';
                            break;
                        case '+float':
                            this.search = searches = !isNaN(searches) ? Math.abs(parseFloat(searches)) : '';
                            break;
                    }
                }
               
                if (this.option.type === 'typeSearch' && this.selects === '' && !this.option.hideSelect) {
                    this.selects = {
                        template: {
                            key: this.option.selectOpt.data[0].text,
                            val: this.option.selectOpt.data[0].value
                        }, obj: this.$parent
                    };
                }
                param = this.option.type === 'typeSearch' && !this.option.hideSelect ?
                    {key: this.option.key ? this.option.key + '.' + this.selects.val : this.selects.val, val: searches,obj: this.$parent, combine: true}
                    :
                    {key: this.option.key, val: searches, obj: this.$parent};
                this.emptyJudge = false;
                if(event && event.keyCode === 13) {
                   this.$emit('enter', param);
                 } else {
                  this.$emit('getInput', param);
                 }
            },
            getChange: function (val) {
                if (this.option.clear) {
                    this.search = '';
                }
                this.selects = val;
                if (this.option.type === 'typeSearch') {
                    this.getControl();
                }
                this.searchGo()
            },
            clearInput: function () {
                this.search = '';
                this.searchGo();
                this.keyUp();
                this.showErr = false;

            },
            resetSearch: function (val) {
                this.search = val;
                let param = {key: this.option.key, val: val, obj: this.$parent};
              //  this.$emit('getInput', param);
                this.showErr = val === '' ? false :  this.showErr;
                this.searchGo();
            },
            showRemove: function () {
                this.searchGo();
                this.removeShow = false;
                this.$emit('cleanInpt',  {key: this.option.key, val: '', obj: this.$parent})
            },
            getDefaultSelect: function (val) {
                let param = {key: this.option.key ? this.option.key + '.' + val.val : val.val, val: this.option.value || this.search, obj: this.$parent, combine: true};
                this.$emit('getInput', param);
            }
        },
        components: {
            selects
        },
        computed: {
            regulars: function () {
                return this.getRegexGroup()
            }
        },
        mounted: function () {
            this.holders = this.option.errorMsg;
            if (typeof this.option.value !== 'undefined') {
                this.search = this.option.value;
            }
            if (this.option.type === 'typeSearch' && !this.option.hideSelect) {
                this.searchType = this.option.selectOpt.data[0].value;
            }
            this.searchGo();
            this.emptyJudge = false;
        },
        watch: {
            'option.value': {
                handler(val, oldVal) {
                    if (!this.option.noWatch) {
                        this.search = val;
                        this.searchGo();
                    }

                }
            },
            'search': {
                handler(val) {
                    if (this.option.regular === 'EUI') {
                        this.search = this.search.replace(/[^\dA-Za-z]/gi, '').replace(/([\dA-Za-z]{2})((?=\d)|(?=[A-Z])|(?=[a-z]))/g,'$1-');
                    }
                    if (typeof val !== 'undefined'  && val !== null && (val.toString().length > this.option.length)) {
                        this.showErr = true
                    }
                }
            }
        }
    }
</script>
<style lang="scss">
    @import "../../assets/main.scss";
    $borderColor:  #227fdf;
    .myInput {
        float: left;
        position: relative;
        input, textarea{
        color:white;
        border:1.5px solid;
        border-color:$borderColor;
        background: rgba(#227fdf, 0.2);
          border-radius:0.2em;
      //   background: #4d5f77;
       // background: rgba(255,255,255, 0.2);
      // color:white;
      //    border-color:transparent;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
          word-break: keep-all;
          padding-right: 2em;
       }
      svg{
        font-size: 1.4em
      }
     }

    .input-container:after {
    @extend .clearElement
    }

    .date-picker:after {
    @extend .clearElement
    }

    .inputSpan {
        position: absolute!important;
        right: 0.625em;
        top: 32%!important;
        cursor: pointer;
        font-size: 0.625em;
        color:white;
    }


    .myInput .typeSelect {
        width: 6.25em;
        .selectBtn{
           border-top-right-radius: 0!important;
           border-bottom-right-radius: 0!important;
           border-right:0!important;
      }
    }
    .myInput .typeSelect + input.selectInput{
      border-radius:0!important;
      border-top:1.5px solid $borderColor;
      border-bottom:1.5px solid $borderColor;
      border-left:0
    }

    .myInput span {
        cursor: pointer
    }

    input.disabled-input {
      //  border: 0;
        box-shadow: 0 0 0 transparent;
        cursor:  not-allowed;
    }

    input.error-input {
        border-color: #f15649!important;
    }
    input.selectInput{
      float:left;
      border-right:0!important;
      border-radius: 0!important;
      border: 1.5px solid  $borderColor;
    }
    input.selectInput:hover{
      box-shadow: none;
      border-color: $borderColor!important;
    }
 .input-container  .searchBtn{
     border-radius: 0.2em;
     border-top-left-radius: 0!important;
     border-bottom-left-radius: 0!important;
     width:47px;
     float:left;
     background:rgba(#3588fa,0.2);
     border-right:1.5px solid $borderColor;
     border-top:1.5px solid $borderColor;
     border-bottom:1.5px solid $borderColor;
     color: #17263C;
    // border-color:transparent;
     i.hasIcon{
       margin-top:-1%
     }
   }
  .searchBtn:hover{
    color: white
  }
  input:hover,textarea:hover{
    box-shadow:none!important
  }
  .splitSpan{
    position: absolute;
    left: 0;
    font-size: 1.9em;
    color: #67768B;
    top: calc(50% - 0.5em);
    height: 1em;
    display: block;
  //  background: #6B6B84;
    background: #3588fa;
    width: 1.2px;
  }
  .inputBtns{
     float:right!important;
     font-size:0.83em
  }
  .combine-input{
     border-top-right-radius:0!important;
     border-bottom-right-radius:0!important;
     overflow:hidden;
     white-space:nowrap;
     text-overflow:ellipsis
  }
  .combine-input:hover{
    // border:0;
     box-shadow:none;

  }
</style>



