const Login = resolve => require(['../component/Login.vue'], resolve);
const Main = resolve => require(['../component/Main.vue'], resolve);
const Staff = resolve => require(['../component/files/staff/Main.vue'], resolve);
const Face = resolve => require(['../component/Face.vue'], resolve);
const ChangeMeter = resolve => require(['../component/files/change/Main.vue'], resolve);
const Equipment = resolve => require(['../component/files/equipment/Main.vue'], resolve);
const Account = resolve => require(['../component/costControl/account/Main.vue'], resolve);
const ChargePrice = resolve => require(['../component/costControl/payment/chargePrice.vue'], resolve);
const Payment = resolve => require(['../component/costControl/payment/Main.vue'], resolve);
const Price = resolve => require(['../component/costControl/price/Main.vue'], resolve);
const NormalHistoricalData = resolve => require(['../component/realTime/historicalData/NormalHistoricalData.vue'], resolve);
const SpecialHistoricalData = resolve => require(['../component/realTime/historicalData/SpecialHistoricalData.vue'], resolve);
const NormalMeasure = resolve => require(['../component/realTime/measure/NormalMeasure.vue'], resolve);
const SpecialMeasure = resolve => require(['../component/realTime/measure/SpecialMeasure.vue'], resolve);
const RemoteControl = resolve => require(['../component/costControl/remoteControl/Main.vue'], resolve);
const Configuration = resolve => require(['../component/systemConfig/configuration/Main.vue'], resolve);
const Role = resolve => require(['../component/systemConfig/role/Main.vue'], resolve);
const Department = resolve => require(['../component/systemConfig/Department/Main.vue'], resolve);
const Users = resolve => require(['../component/systemConfig/users/Main.vue'], resolve);
const dayFreeze = resolve => require(['../component/realTime/freezeData/dayFreeze.vue'], resolve);
const impFreeze = resolve => require(['../component/realTime/freezeData/impFreeze.vue'], resolve);
const MeasureAgain = resolve => require(['../component/realTime/measureAgain/Main.vue'], resolve);
const TotalSearch = resolve => require(['../component/analyse/TotalSearch.vue'], resolve);

const LineLoss = resolve => require(['../component/analyse/LineLoss.vue'], resolve);
const efficiencyAnalysis = resolve => require(['../component/analyse/efficiencyAnalysis/Main.vue'], resolve);
// const Information = resolve => require(['../component/Information.vue'], resolve);

const CostList = resolve => require(['../component/analyse/CostList.vue'], resolve);
const Ranking = resolve => require(['../component/analyse/Ranking.vue'], resolve);
const ExceptionAccount = resolve => require(['../component/analyse/ExceptionAccount.vue'], resolve);
const Alarm = resolve => require(['../component/analyse/tabPage/alarm.vue'], resolve);
const ChangeMeterList = resolve => require(['../component/analyse/tabPage/changeMeter.vue'], resolve);
const SpecialAttention = resolve => require(['../component/analyse/SpecialAttention.vue'], resolve);
const CommInfo = resolve => require(['../component/analyse/tabPage/commInfo.vue'], resolve);
const MeasureList = resolve => require(['../component/analyse/tabPage/measure.vue'], resolve);
const OnOff = resolve => require(['../component/analyse/tabPage/onOff.vue'], resolve);
const PayList = resolve => require(['../component/analyse/tabPage/payment.vue'], resolve);
const OperateLog = resolve => require(['../component/systemConfig/log/operateLog.vue'], resolve);
const planList = resolve => require(['../component/plan/main.vue'], resolve);
const addPlan = resolve => require(['../component/plan/addPlan.vue'], resolve);

const ChargingEquipCostAcc = resolve => require(['../component/widsomApplication/costAccount/Main.vue'], resolve);
const ChargingEquipMana = resolve => require(['../component/widsomApplication/equipmentMana/Main.vue'], resolve);
const ChargingStaffMana = resolve => require(['../component/widsomApplication/staffMana/Main.vue'], resolve);
const ChargingEquipStateMon = resolve => require(['../component/widsomApplication/stateMonitoring/Main.vue'], resolve);
const ChargingIC = resolve => require(['../component/widsomApplication/ICCard/Main.vue'], resolve);
const ChargingLineAnalysis = resolve => require(['../component/widsomApplication/ChargingLineAnalysis/Main.vue'], resolve);
const ChargingPricePlan = resolve => require(['../component/widsomApplication/pricePlan/Main.vue'], resolve);

const DeviceList = resolve => require(['../component/smokeFeeling/DeviceList/Main.vue'], resolve);
const stateMonitoring = resolve => require(['../component/smokeFeeling/stateMonitoring/Main.vue'], resolve);

const Returns = resolve => require(['../component/costControl/payment/returns.vue'], resolve);
const ReturnsList = resolve => require(['../component/costControl/payment/returnList.vue'], resolve);
const DownloadReport = resolve => require(['../component/exports/downloadReport.vue'], resolve);
const Pay = resolve => require(['../component/exports/lists/payment.vue'], resolve);
const Refund = resolve => require(['../component/exports/lists/returns.vue'], resolve);



Vue.use(Router);



export default new Router({
  // mode: 'history',
  // hashbang: false,
  // history: true,
  routes: [{
    path: '/',
    component: Login,
    name: 'Login'
  }, {
    path: '/Main',
    name: 'Main',
    component: Main,
    children: [{
        path: '/Main/ChargingEquipCostAcc',
        components: {
          ChargingEquipCostAcc: ChargingEquipCostAcc
        },
        name: 'ChargingEquipCostAcc',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/ChargingEquipMana',
        components: {
          ChargingEquipMana: ChargingEquipMana
        },
        name: 'ChargingEquipMana',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/ChargingStaffMana',
        components: {
          ChargingStaffMana: ChargingStaffMana
        },
        name: 'ChargingStaffMana',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/ChargingEquipStateMon',
        components: {
          ChargingEquipStateMon: ChargingEquipStateMon
        },
        name: 'ChargingEquipStateMon',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/ChargingPricePlan',
        components: {
          ChargingPricePlan: ChargingPricePlan
        },
        name: 'ChargingPricePlan',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/ChargingIC',
        components: {
          ChargingIC: ChargingIC
        },
        name: 'ChargingIC',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/ChargingLineAnalysis',
        components: {
          ChargingLineAnalysis: ChargingLineAnalysis
        },
        name: 'ChargingLineAnalysis',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/DeviceList',
        components: {
            DeviceList: DeviceList
        },
        name: 'DeviceList',
        meta: {
            keepAlive: true
        }
    }, {
        path: '/Main/stateMonitoring',
        components: {
            stateMonitoring: stateMonitoring
        },
        name: 'stateMonitoring',
        meta: {
            keepAlive: true
        }
    }, {
        path: '/Main/Staff',
        components: {
          Staff: Staff
        },
        name: 'Staff',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/MeasureAgain',
        components: {
          MeasureAgain: MeasureAgain
        },
        name: 'MeasureAgain',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/Face',
        components: {
          Face: Face
        },
        name: 'Face',
        meta: {
          keepAlive: false
        }
      }, {
        path: '/Main/DownloadReport',
        components: {
          DownloadReport: DownloadReport
        },
        name: 'DownloadReport',
        meta: {
          keepAlive: true
        },
        children: [{
          path: '/Main/DownloadReport/Pay',
          component: Pay,
          meta: {
            keepAlive: false
          }
        }, {
          path: '/Main/DownloadReport/Refund',
          component: Refund,
          meta: {
            keepAlive: false
          }
        }]
      }, {
        path: '/Main/ChangeMeter',
        components: {
          ChangeMeter: ChangeMeter
        },
        name: 'ChangeMeter',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/Equipment',
        components: {
          Equipment: Equipment
        },
        name: 'Equipment',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/Account',
        components: {
          Account: Account
        },
        name: 'Account',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/Payment',
        components: {
          Payment: Payment
        },
        name: 'Payment',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/ReturnsList',
        components: {
          ReturnsList: ReturnsList
        },
        name: 'ReturnsList',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/ChargePrice',
        components: {
          ChargePrice: ChargePrice
        },
        name: 'ChargePrice',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/Returns',
        components: {
          Returns: Returns
        },
        name: 'Returns',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/Price',
        components: {
          Price: Price
        },
        name: 'Price',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/RemoteControl',
        components: {
          RemoteControl: RemoteControl
        },
        name: 'RemoteControl',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/Configuration',
        components: {
          Configuration: Configuration
        },
        name: 'Configuration',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/Role',
        components: {
          Role: Role
        },
        name: 'Role',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/Users',
        components: {
          Users: Users
        },
        name: 'Users',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/Department',
        components: {
          Department: Department
        },
        name: 'Department',
        meta: {
          keepAlive: false
        }
      }, {
        path: '/Main/dayFreeze',
        components: {
          dayFreeze: dayFreeze
        },
        name: dayFreeze,
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/impFreeze',
        components: {
          impFreeze: impFreeze
        },
        name: impFreeze,
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/SpecialAttention',
        components: {
          SpecialAttention: SpecialAttention
        },
        name: 'SpecialAttention',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/Ranking',
        components: {
          Ranking: Ranking
        },
        name: 'Ranking',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/CostList',
        components: {
          CostList: CostList
        },
        name: 'CostList',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/ExceptionAccount',
        components: {
          ExceptionAccount: ExceptionAccount
        },
        name: 'ExceptionAccount',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/OperateLog',
        components: {
          OperateLog: OperateLog
        },
        name: 'OperateLog',
        meta: {
          keepAlive: true
        }
      },

      {
        path: '/Main/NormalHistoricalData',
        components: {
          NormalHistoricalData: NormalHistoricalData
        },
        name: 'NormalHistoricalData',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/SpecialHistoricalData',
        components: {
          SpecialHistoricalData: SpecialHistoricalData
        },
        name: 'SpecialHistoricalData',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/planList',
        components: {
          planList: planList
        },
        name: 'planList',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/addPlan',
        components: {
          addPlan: addPlan
        },
        name: 'addPlan',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/NormalMeasure',
        components: {
          NormalMeasure: NormalMeasure
        },
        name: 'NormalMeasure',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/SpecialMeasure',
        components: {
          SpecialMeasure: SpecialMeasure
        },
        name: 'SpecialMeasure',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/LineLoss',
        components: {
          LineLoss: LineLoss
        },
        name: 'LineLoss',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/efficiencyAnalysis',
        components: {
          efficiencyAnalysis: efficiencyAnalysis
        },
        name: 'LineLossData',
        meta: {
          keepAlive: true
        }
      }, {
        path: '/Main/TotalSearch',
        components: {
          TotalSearch: TotalSearch
        },
        meta: {
          keepAlive: true
        },
        children: [{
          path: '/',
          component: CommInfo,
          name: 'CommInfo'
        }, {
          path: '/Main/TotalSearch/Alarm',
          component: Alarm,
          name: 'Alarm'
        }, {
          path: '/Main/TotalSearch/ChangeMeterList',
          component: ChangeMeterList,
          name: 'ChangeMeterList'
        }, {
          path: '/Main/TotalSearch/MeasureList',
          component: MeasureList,
          name: 'MeasureList'
        }, {
          path: '/Main/TotalSearch/OnOff',
          component: OnOff,
          name: 'OnOff'
        }, {
          path: '/Main/TotalSearch/PayList',
          component: PayList,
          name: 'PayList'
        }]
      }
    ]
  }]
})


