<template>
    <div>
        <li >
            <span class="menuLi" v-if="model.url">
                <router-link  :to="{path: model.url}">
                   <span  class="model.icon"></span>
                   {{model.orgName}}
                </router-link>
            </span>

            <span class="menuLi" :class="{noCheck:  noCheck && model[noCheck.key] == noCheck.value}" v-if="typeof model.url == 'undefined'">
              <a  @mousedown.stop="!(noCheck && model[noCheck.key] == noCheck.value) && sendMsg(model)">
                <i v-if="isFolder" class="hasIcon" style= "margin-top: 0.1em;"><icon :name="open ? 'folder-open': 'folder'" scale="0.999"></icon></i>
                <i v-if="!isFolder" class="hasIcon"  style= "margin-top: 0.1em;"><icon name="file-text" scale="0.999"></icon></i>
                <template v-if="MultiSelect">
                      <input type="checkbox"  class="checkBox"  v-model="select" @click.stop="getSelect(model)" />
                      <label class="myCheckbox myCheckBox totalCheck" >{{model[text]}}</label>
                </template>
                <template v-else>
                      <span class="menuLi_span" :title="model[text]">{{model[text]}}</span>
                </template>
                <i class="hasIcon" style="float:right;margin-top:0" v-if="isFolder && !open"   @mousedown.stop="toggle()"><icon name="angle-right"></icon></i>
                <i class="hasIcon"  style="float:right;margin-top:0" v-if="isFolder && open" @mousedown.stop="toggle()"><icon name="angle-down"></icon></i>
              </a>
            </span>
          <ul v-show="open" v-if="isFolder"  >
              <template v-for ="(item, $index) in model.children">
                 <treeMenu  :model="item" @getSelectMenu="getSelectMenu" :MultiSelect="MultiSelect" :multiValue="multiValue" :text="text" :noCheck="noCheck" :value="value"></treeMenu>
              </template>
          </ul>
        </li>
    </div>
</template>
<script>
import 'vue-awesome/icons/angle-right'
import 'vue-awesome/icons/angle-down'
import 'vue-awesome/icons/folder-open'
import 'vue-awesome/icons/folder'
import 'vue-awesome/icons/file-text'
    export default{
        name: 'treeMenu',
        props: [
            'model',
            'MultiSelect', 
            'multiValue',
            'text',
            'value',
            'noCheck'
        ],
        data() {
            return {
                open: true,
                select: false,
                sign: true,
                arr: []
            }
        },
        computed: {
            isFolder: function() {
                return this.model.children && this.model.children.length
            }
        },
        methods: {
            toggle: function() {
                if (this.isFolder) {
                    this.$emit('getSelectMenu', '');
                    this.open = !this.open;
                }
            },
            sendMsg: function (model) {
                if(!this.MultiSelect) {
                    this.select = !this.select;
                    this.$emit('getSelectMenu', model);  
                }
            },
            getSelect: function (model) {
                this.select = !this.select;
                this.$emit('getSelectMenu', model);
            },
            getSelectMenu: function (model) {
                this.$emit('getSelectMenu', model);
            }
        },
       created: function () {
        if(this.MultiSelect) {
         this.arr = this.multiValue !== '' ? this.multiValue.toString().split(',') : [];
          for(let i=0;i<this.arr.length;i++) {
            if(this.arr[i] == this.model[this.value]) {
                this.select = true;
             
                break;
            }
          } 
        }       
       }
    }
</script>
<style lang="scss">
    @import "../../assets/main.scss";
    ul.sUl {
        -moz-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        padding-left:15px;
        list-style: none;
        li {
          cursor: pointer;
          width: 100%;
          padding: 0;
          min-width: 100%!important;
          margin: auto;
          float: left;
          text-align: left;
            span{
              line-height: 1.5
            }
            a{
                padding: 2px 14px;
                width: calc(100% - 28px);
                float:left;
                display: block;
                text-decoration: none;
                text-align: left;
                color: white;
                span.menuLi_span{
                    display:block;
                    width: calc(100% - 3em);
                    float:left;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: normal;
                }
                .glyphicon-chevron-right  {
                    float:right;
                    margin-right:0;
                }
            }
            .menuLi{
                display:block;
                input.checkBox{
                  height:1em
                }
                .myCheckBox:before{
                    left: -1.718em;
                    color: rgba(0,0,0,0.8);
                    border-color: rgba(0,0,0,0.8);
                }
                i.hasIcon{
                    margin-right:0.1em
                }
            }
            .menuLi:after{
                content: '';
                display:block;
                width:1px ;
                height:0;
                clear: both;
            }
            .noCheck a{
                color: gray
             }
            ul li a {
                padding: 2px 14px 2px 26px;
                width: calc(100% - 40px);
            }
            ul li ul li a{
                padding: 2px 14px 2px 42px;
                width: calc(100% - 56px);
            }
            ul li ul li ul li a{
                padding: 2px 14px 2px 54px;
                width: calc(100% - 68px);
            }
            ul li ul li ul li ul li  a{
                padding: 2px 14px 2px 66px;
                width: calc(100% - 80px);
            }
        }
        .menuLi a:hover{
         // background:#4C5E76;
          color:white;
          background: #227fdf
        }
    }
    i.icon {
        display: inline-block;
        width: 15px;
        height: 15px;
        background-repeat: no-repeat;
        vertical-align: middle;
    }
    .icon.folder {
        background-image: url(../../assets/img/f2.png);
    }
    .icon.folder-open {
        background-image: url(../../assets/img/f3.png);
    }
    .icon.file-text {
        background-image: url(../../assets/img/f.png);
    }
    .tree-menu li {
        line-height: 1.5;
    }
    .ztree li span.button.center_docu {
        background-position: -56px -18px;
    }

    .ztree li span.button.switch {
        width: 18px;
        height: 18px;
    }

</style>



