import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        balanceTime: '',
        sysName: '',
        count: 1,
        alarmNum: 0,
        deviceType: '',
        selectTab: sessionStorage.selectTab ? JSON.parse(sessionStorage.selectTab) : [{
            menuTitle: '系统首页',
            menuURL: '/Main/Face',
            menuId: -1,
            parentMenuId: -1,
            hasTree: false
        }],
        cMenu: sessionStorage.cMenu || -1,
        systemConfig: localStorage.systemConfig ? JSON.parse(localStorage.systemConfig) : {},
        pMenu: sessionStorage.pMenu || 0,
        topMenu: sessionStorage.topMenu || 0,
        currentParIndex: sessionStorage.currentParIndex || 0,
        customerDeviceIndex: 0, //用户档案当前选中的设备类型下标。。用于档案修改以后刷新页面
        currentSearch: '', //综合查询里面的搜索条件
        customerNo: '', //综合查询当前用户的用户No
        callback: {
            onOff: null,
            collectData: null,
            alarm: null,
            jzqReadCustomerInfo: null,
            jzqHeartBeatSend: null,
            jzqHeartBeatRead: null,
            collectDataStatus: null,
            jzqHeartBeatSendStatus: null,
            jzqHeartBeatReadStatus: null,
            onOffStatus: null,
            jzqReadCustomerInfoStatus: null,
            monitorDeviceList: null,
            onOffDevice: null,
            chargerThreshold: null,
            chargerHeartStep: null,
            setThresholdRes: null,
            setHeartStepRes: null,
            autoUploadMsg: null,
            sendIcCard: null,
            readIcCard: null,
            clearIcCard: null
        },
        setRecall: null,
        linkCheck: 'linkCheck',
        socketLink: false,
        hasTree: sessionStorage.hasTree,
        currentSelectOrg: '',
        currentSearchType: 0,
        socketUrl: localStorage.socketUrl ? localStorage.socketUrl : '',
        selectedUser: '',
        selectedNode: '',
        openNode: [], //用户档案树选中的节点
        operateStatus: { //mu召测（用户，搜索类型0），Md召测（设备，搜索类型1）
            pageNumMu: 1,
            pageNumMd: 1,
            guidMu: '',
            guidMd: '',
        },
        hasBlur: false,
        nodeSelect: [],
        downloadLink: '',
        copyright: '',
        appVersion: localStorage.appVersion ? localStorage.appVersion : '',
        windowWidth: 0,
        windowHeight: 0
    }
})


