<template>
  <div class="date-picker"  
    :style="{width: dateOpt.width,outline: 'none', float: 'left'}"  
    :class="{customerOne: dateOpt.customerType}"
     >
      <template v-if="dateOpt.type === 'doubleDate'|| type === 'doubleDate' ">
        <button :style="{width: 'calc(50% - 1.1em)'}" class=" date-button"  @click="showCalendar"  ref="dateBtnS">
          {{date.split('至').length > 0 ? date.split('至')[0] : ''}}
          <i class="hasIcon" style="float:right;margin-right:0.2em;margin-top:-1px;font-size:1.3em"><icon name="calendar"></icon></i>
        </button>
        <span style="float:left;padding:0.7em 0.5em"> 至 </span>
        <button :style="{width:  'calc(50% - 1.1em)'}" class=" date-button"  @click="showCalendar(1)"  ref="dateBtnE">
          {{date.split('至').length > 0 ? date.split('至')[1] : ''}}
          <i class="hasIcon" style="float:right;margin-right:0.2em;margin-top:-1px;font-size:1.3em"><icon name="calendar"></icon></i>
        </button>
      </template>
      <template v-else>
        <button :style="{width: '100%'}" class=" date-button"  @click="showCalendar"  ref="dateBtnS">
          {{date}}
        <i class="hasIcon icon-date" v-if="date != '' && dateOpt.hasRemove" style="margin-right:-0.1em;" @click.stop="removeSelectData">
          <icon name="remove"></icon>
        </i>
        <i class="hasIcon icon-date" v-if="date == '' || !dateOpt.hasRemove"  style="margin-right:-0.1em;">
          <icon name="calendar"></icon>
        </i>
        </button>
      </template>
      <div    tabindex="1"   @blur="hidesCalendars" :class="{'myCalendar': true, 'doubleCalendar': (dateOpt.type === 'doubleDate' || type === 'doubleDate') && false}"  v-show="showCalendars && !endShow" :style="calendarStyle" ref="dataConS">
          <div class="calendars">
            <div class="calendar-header" v-if="startMonth !== ''">
                <i class="date-before" @mousedown.stop="dateOpt.onlyMonth ? beforeYear('start') : beforeMonth('start')" ><icon name="angle-left" scale="1.3"></icon></i>
                <span v-if="!dateOpt.onlyMonth" class="date-now">{{months[startMonth].text}} {{startYear}}年</span>
                <span v-if="dateOpt.onlyMonth" class="date-now"> {{startYear}}年</span>
                <i class="date-after" @mousedown.stop="dateOpt.onlyMonth ? nextYear('start') : nextMonth('start')"><icon name="angle-right" scale="1.3"></icon></i>
            </div>
            <div class="calendar-content" >
              <template v-if="!dateOpt.onlyMonth">
                <span v-for="(days, $weekIndex) in weeks">{{days}}</span>
                <span    v-for="(dates, $dateIndex) in startDays" :class="{'day-span': dates.val!='', 'daySelect': startIndex == $dateIndex && !dates.disabled && ((startLast.split('-')[0] == startYear && parseInt(startLast.split('-')[1]) - 1 == startMonth && (parseInt(startLast.split('-')[2]) + getWeeks('start')) == $dateIndex && !showstartIndex) || showstartIndex), 'disableDay': dates.disabled}" @mousedown="!dates.disabled && getDays('start', $dateIndex)">{{dates.val}}</span>
              </template>
              <template v-else>
                <span  :style="{width: 'calc((100% - 1.875em) / 5)'}" v-for="(dates, $dateIndex) in startMonths" :class="{'day-span': dates.val!='', 'daySelect': startIndex == $dateIndex && !dates.disabled && ((startLast.split('-')[0] == startYear && parseInt(startLast.split('-')[1]) - 1 == $dateIndex && !showstartIndex) || showstartIndex), 'disableDay': dates.disabled}" @mousedown="!dates.disabled && getDays('start', $dateIndex)">{{dates.val}}</span>
              </template>
            </div>
          </div>
          <!--<div  v-if="dateOpt.type === 'doubleDate'|| type === 'doubleDate'" class="calendar-footer">-->
              <!--<span >起</span>-->
              <!--<input type="text" class="calendar-input" :value="startDate" disabled>-->
              <!--<span > 止 </span>-->
              <!--<input class="calendar-input" type="text"  :value="endDate" disabled>-->
              <!--<button @click="getAllDate" class="make-sure">确定</button>-->
              <!--<button @click="cancelDate" class="make-cancel">取消</button>-->
          <!--</div>-->
      </div>
      <div  v-if="(dateOpt.type === 'doubleDate'|| type === 'doubleDate') "   ref="dataConE" tabindex="1"   @blur="hidesCalendars" :class="{'myCalendar': true, 'doubleCalendar': (dateOpt.type === 'doubleDate' || type === 'doubleDate') && false}"  v-show="!showCalendars && endShow" :style="{'font-size': dateOpt.fontSize || '0.875em', right: 0}"  >
       <div  class="calendars" :style="{width: dateOpt.onlyMonth ? '14em' : '18em'}">
      <div class="calendar-header" v-if="endMonth !== ''">
        <i class="date-before" @mousedown.stop="dateOpt.onlyMonth ? beforeYear('end') : beforeMonth('end')" ><icon name="angle-left" scale="1.3"></icon></i>
        <span v-if="!dateOpt.onlyMonth" class="date-now">{{months[endMonth].text}} {{endYear}}年</span>
        <span v-if="dateOpt.onlyMonth" class="date-now"> {{endYear}}年</span>
        <i class="date-after" @mousedown.stop="dateOpt.onlyMonth ? nextYear('end') : nextMonth('end')"><icon name="angle-right" scale="1.3"></icon></i>
      </div>
      <div class="calendar-content">
        <template v-if="!dateOpt.onlyMonth">
          <span v-for="(days, $weekIndex) in weeks">{{days}}</span>
          <span v-for="(dates, $dateIndex) in endDays" :class="{'day-span': dates.val != '', 'daySelect': endIndex == $dateIndex && !dates.disabled && ((endLast.split('-')[0] == endYear && parseInt(endLast.split('-')[1]) - 1 == endMonth && (parseInt(endLast.split('-')[2]) + getWeeks('end')) == $dateIndex && showendIndex ) || showendIndex), 'disableDay': dates.disabled}" @mousedown="!dates.disabled && getDays('end', $dateIndex)">{{dates.val}}</span>
        </template>
        <template v-else>
             <span :style="{width: 'calc((100% -   4em) / 6)'}"  v-for="(dates, $dateIndex) in endMonths" :class="{'day-span': dates.val != '', 'daySelect': endIndex == $dateIndex && !dates.disabled && ((endLast.split('-')[0] == endYear && parseInt(endLast.split('-')[1]) - 1 == endMonth && (parseInt(endLast.split('-')[2]) + getWeeks('end')) == $dateIndex && showendIndex ) || showendIndex), 'disableDay': dates.disabled}" @mousedown="!dates.disabled && getDays('end', $dateIndex)">{{dates.val}}</span>
        </template>
      </div>
    </div>
      </div>
  </div>
</template>

<script>
import 'vue-awesome/icons/angle-right'
import 'vue-awesome/icons/angle-left'
import 'vue-awesome/icons/calendar'
export default {
  props: {
      type: {
           type: String,
          'default': 'angleDate'
      },
      dateOpt: {
          type: Object,
          'default'() {
              return {
                  width: '230px',
                  type: '',
                  onlyDay: '',
                  key: '',
                  hasRemove: false //是否删除选中的日期，返回空
              }
          }
      },
      dateFormatter: {
          type: String,
          'default': 'yyyy-mm-dd'
      },
      monthFormatter: {
          type: String,
          'default': 'yyyy-mm'
      },
      disabledSDate: {
          type: String,
          'default': ''
      },
      disabledEDate: {
          type: String,
          'default': ''
      },
     //true+true，向前不能选 false+true:向后不能选  true+false: 都不能选 false+false: 都可以选
      showBeforeDisabled: {
          type: Boolean,
          'default': false
      },
      showAfterDisabled: {
          type: Boolean,
          'default': true
      }
  },
  data () {
    return {
        calendarStyle: {},
        endShow: false,
        flag: true,
        showCalendars: false,
        months: [
            {text: '一月', val: 1},
            {text: '二月', val: 2},
            {text: '三月', val: 3},
            {text: '四月', val: 4},
            {text: '五月', val: 5},
            {text: '六月', val: 6},
            {text: '七月', val: 7},
            {text: '八月', val: 8},
            {text: '九月', val: 9},
            {text: '十月', val: 10},
            {text: '十一月', val: 11},
            {text: '十二月', val: 12}
        ],
        weeks:['日', '一', '二', '三', '四', '五', '六'],
        startYear: '',
        startMonth: '',
        endYear: '',
        endMonth: '',
        startDay: '',
        endDay: '',
        date: '',
        showstartIndex: true,
        showendIndex: true,
        startIndex: -1,
        endIndex: -1,
        startDays: [],
        endDays: [],
        startMonths: [],
        endMonths: [],
        startDate: '',
        endDate: '',
        startDisable: '',
        endDisable: '',
        startLast: '',
        endLast: ''
    }
  },

  computed: {
  },
  methods: {
      resetPosition: function () {
         let fas = this.$refs.dateBtnS;
         let fae = this.$refs.dateBtnE;
         let pos = fas.getBoundingClientRect();
         let poe = fae ? fae.getBoundingClientRect() : {};
         let c1 = this.$refs.dataConS;
         let c2 = this.$refs.dataConE;
         if(c1) {
           c1.style.left = pos.left + 'px';
           c1.style.top = (pos.top  + fas.clientHeight )+ 'px';
           c1.style.width = '18em'
         }
         if(c2) {
           c2.style.left = 'calc(' + poe.left + 'px - (18em - ' + fae.clientWidth+'px))';
           c2.style.top = (poe.top  + fae.clientHeight )+ 'px';
           c2.style.width = '18em'
         }
      },
      windowChange: function () {
         
           this.resetPosition()
      },
      removeSelectData: function () {
        this.date =  '';
        this.resetDate();
        this.$emit('getDatePicker', {key: this.dateOpt.key, val: this.date, obj: this.$parent});
      },
      getChange: function (val) {
        if (this.dateOpt.onlyMonth) {
          this.getAllMonth(val);
        } else {
          this.getAllDays(val);

        }
      },
      resetDate: function () {
          this.startDate = this.endDate = this.startDisable = this.endDisable = '';
          this.startIndex = this.endIndex = -1;
        if (this.dateOpt.onlyMonth) {
          this.getAllMonth('start');
          this.getAllMonth('end')
        } else {
          this.getAllDays('start');
          this.getAllDays('end');
        }
      },
      cancelDate: function (){
          this.showCalendars = false;
          this.resetDate();
          this.startDate = this.startLast;
          this.endDate = this.endLast ;
      },
      getAllDate: function () {
          if (this.startDate === '' || this.endDate === '') {
              alert('起始终止时间都不能为空')
          } else {
              this.date = this.startDate + '至' + this.endDate;
              this.startLast = this.startDate;
              this.endLast = this.endDate;
              this.resetDate();
              this.getNowDate();
              this.hideCalendar()
          }
      },
      hideCalendar: function () {
       
          this.showCalendars = false;
          this.endShow = false;
      },
      hidesCalendars: function () {

          this.showCalendars = false;
       // this.endShow = false;
      },
      getWeeks (val) {
          let date = new Date(this[val + 'Year'], this[val + 'Month'], 1);
          return date.getDay()
      },
      daysInMonth (year, month) {
          return new Date(Date.UTC(year, month + 1, 0)).getUTCDate()
      },
      getNowDate: function () {
          this.startDate = this.getFormatter(this.startYear, this.startMonth + 1, this.startDay);
          if (this.type === 'doubleDate') {
              this.endDate = this.getFormatter(this.endYear, this.endMonth + 1, this.endDay);
              this.date = this.startDate +  '至' +this.endDate;
          } else {
              this.date = this.startDate ;
          }
          this.$emit('getDatePicker', {key: this.dateOpt.key, val: this.date, obj: this.$parent});
           this.hideCalendar();
          return this.date
      },
      getFormatter: function (y, m, d) {
          let formatter =  !this.dateOpt.onlyMonth ? this.dateFormatter  : this.monthFormatter;
          return this.dateOpt.onlyMonth ? formatter.replace(/yyyy/,this.fillZero(y)).replace(/mm/, this.fillZero(m)).replace(/dd/, '') : formatter.replace(/yyyy/,this.fillZero(y)).replace(/mm/, this.fillZero(m)).replace(/dd/, this.fillZero(d));
      },
      fillZero: function (val) {
          return val < 10 ? '0' + val : val
      },
      beforeMonth: function (val) {

          if (this[val + 'Month'] === 0) {
              this[val + 'Year'] --;
              this[val + 'Month'] = 11;
          } else {
              this[val + 'Month'] --
          }
          this.flag = false;
          this['show' + val + 'Index'] = (parseInt(this[val + 'Date'].split('-')[1]) - 1 == (this[val + 'Month'])) && this[val + 'Date'].split('-')[0] == this[val + 'Year'] && this[val + 'Date'].split('-')[2] == this[val + 'Day'] ;
      },
      beforeYear: function (val) {
        this[val + 'Year'] --;
        this.flag = false;
        this['show' + val + 'Index'] = (parseInt(this[val + 'Date'].split('-')[1]) - 1 == (this[val + 'Month'])) && this[val + 'Date'].split('-')[0] == this[val + 'Year'];
      },
      nextYear: function (val) {
        this[val + 'Year'] ++;
        this.flag = false;
        this['show' + val + 'Index'] = (parseInt(this[val + 'Date'].split('-')[1]) - 1 == (this[val + 'Month'])) && this[val + 'Date'].split('-')[0] == this[val + 'Year'] ;
    },
      nextMonth: function (val) {
          if (this[val + 'Month'] === 11) {
              this[val + 'Year'] ++;
              this[val + 'Month'] = 0;
          } else {
              this[val + 'Month'] ++;
          }
          this.flag = false;
          this['show' + val + 'Index'] = (parseInt(this[val + 'Date'].split('-')[1]) - 1 == (this[val + 'Month'])) && this[val + 'Date'].split('-')[0] == this[val + 'Year'] && this[val + 'Date'].split('-')[2] == this[val + 'Day'] ;
      },
      showCalendar: function (flags) {
          flags === 1 ? '' : this.resetPosition();
          this.endShow = flags === 1 ? !this.endShow : false;
          if (!this.flag) {
              this.getSelectDay(this.startLast, this.endLast);
          }
          this.showCalendars = flags === 1 ? false : !this.showCalendars ;
      },
      getSelectDay: function (starts, ends) {
          this.startDate = starts;
          this.endDate = ends;
          let start = this.startDate.split('-');
          let end = this.endDate.split('-');
          this.startMonth = parseInt(start[1])-1;
          this.endMonth = parseInt(end[1]) -1;
          this.startYear = start[0];
          this.endYear = end[0];
          this.startDay =  parseInt(start[2]);
          this.startIndex = this.startDay - 1 + this.getWeeks('start');
          this.endDay = parseInt(end[2]);
          this.endIndex =  this.endDay  - 1  + this.getWeeks('end');
      },
      init: function () {
          let date;
          this.calendarStyle['font-size'] = this.dateOpt.fontSize || '0.875em' ;
          this.calendarStyle[this.dateOpt.align || 'left'] = 0;
          if (typeof this.dateOpt.value === 'undefined' || (typeof this.dateOpt.value !== 'undefined' && this.dateOpt.value == '')) {
              date = new Date();
              this.startMonth = this.endMonth = date.getMonth();
              this.startYear = this.endYear = date.getFullYear();
              this.startDay = this.endDay = date.getDate();
              this.startLast = this.startYear + '-' + (this.startMonth + 1) + (this.dateOpt.onlyMonth ? ('') : ('-' +  this.startDay));
              
              this.endIndex = this.startIndex = this.dateOpt.onlyMonth ? (date.getMonth()) : (date.getDate() - 1 + this.getWeeks('start'));
              this.getNowDate();
          } else if (this.type === 'doubleDate' && typeof this.dateOpt.value === 'object'){
              this.getSelectDay(this.dateOpt.value.startDate, this.dateOpt.value.endDate);
              this.startLast = this.dateOpt.value.startDate;
              this.endLast = this.dateOpt.value.endDate;
              this['endDisable'] =  this.startDate;
              this['startDisable'] =  this.endDate;
              this.date =  this.startDate + '至' + this.endDate
              this.$emit('getInitDatePicker', {key: this.dateOpt.key, val: this.date, obj: this.$parent});
          } else {
              this.getSelectDay(this.dateOpt.value, this.dateOpt.value);
              this.startLast = this.dateOpt.value;
              this.endLast = this.dateOpt.value;
              this.date  = this.dateOpt.value;
          }
          if (this.dateOpt.onlyMonth) {
            this.getAllMonth('start');
            this.getAllMonth('end')
          } else {
            this.getAllDays('start');
            this.getAllDays('end');
            
          }

      },
      getYMD: function (val) {
          return this[val].split("-");
      },
      getAllMonth: function (val) {
        let date = new Date();
        let nowMonth = date.getMonth() + 1;
        let nowYear = date.getFullYear();
        let month, year, flags = false;
        if (this[val + 'Disable'] !== '') {
          let days = this.getYMD(val + 'Disable');
          year = days[0];
          month = days[1];
        }
        let Arr = [];
        for (let j = 1; j<=12; j++) {
          let signs =  (this[val + 'Year'] > nowYear) ||
            (this[val + 'Year'] == nowYear && (this[val + 'Month'] + 1) > nowMonth);
          if (!this.showBeforeDisabled) {
            signs = signs || (this[val + 'Year'] == nowYear && j > nowMonth);
          } else {
            signs = signs || (this[val + 'Year'] == nowYear && j >= nowMonth);
          }
          if (this[val + 'Disable'] !== '') {
            if(val === 'start') {
              flags = this[val + 'Year'] > year ||
                (this[val + 'Year'] == year && month < j) || signs;
              Arr.push({disabled: this.showBeforeDisabled ? !flags: flags, val: j});
            } else {
              flags = this[val + 'Year'] < year ||
                (this[val + 'Year'] == year && month > j) || signs;
              Arr.push({disabled: this.showBeforeDisabled ? !flags: flags, val: j});
            }
          } else {
            Arr.push({disabled: this.showBeforeDisabled ? !signs : signs, val: j});
          }
        }
        this[val + 'Months'] = Arr;
      },
      getAllDays: function (val) {
          let date = new Date();
          let nowDay = date.getDate();
          let nowMonth = date.getMonth() + 1;
          let nowYear = date.getFullYear();
          let month, year, day, flags = false;
          if (this[val + 'Disable'] !== '') {
              let days = this.getYMD(val + 'Disable');
              year = days[0];
              month = days[1];
              day = days[2]
          }
          let days = this.daysInMonth(this[val + 'Year'], this[val + 'Month']);
          let dayArr = [];
          for (let i = 0; i < this.getWeeks(val); i++) {
              dayArr.push({disabled: false, val: ''});
          }
          for (let i = 1; i <= days; i++) {
              let sign =  (this[val + 'Year'] > nowYear) ||
                  (this[val + 'Year'] == nowYear && (this[val + 'Month'] + 1) > nowMonth) ||
                  (this[val + 'Year'] == nowYear && (this[val + 'Month'] + 1) == nowMonth && i > nowDay) ;

             
                if (!this.showBeforeDisabled) {
                  sign = sign || (this[val + 'Year'] == nowYear && (this[val + 'Month'] + 1) == nowMonth && i > nowDay);
                } else {
                  sign = sign || (this[val + 'Year'] == nowYear && (this[val + 'Month'] + 1) == nowMonth && i >= nowDay);
                }
              
               sign = sign && this.showAfterDisabled;
              
              if (this[val + 'Disable'] !== '' ) {
                  if(val === 'start') {
                      sign = this[val + 'Year'] > year ||
                          (this[val + 'Year'] == year && (this[val + 'Month'] + 1) > month) ||
                          (this[val + 'Year'] == year && (this[val + 'Month'] + 1) == month && day < i) || sign;
                  } else {
                      sign = this[val + 'Year'] < year ||
                          (this[val + 'Year'] == year && (this[val + 'Month'] + 1) < month) ||
                          (this[val + 'Year'] == year && (this[val + 'Month'] + 1) == month && day > i) || sign;
                  }
                 
                }
               if(this.disabledSDate !== '') {
                  let arr = this.disabledSDate.split('-');
                  let signs = this[val + 'Year'] > parseInt(arr[0]) ||
                          (this[val + 'Year'] == parseInt(arr[0]) && (this[val + 'Month'] + 1) > parseInt(arr[1])) ||
                          (this[val + 'Year'] == parseInt(arr[0]) && (this[val + 'Month'] + 1) == parseInt(arr[1]) && parseInt(arr[2])< i)

                  sign = !signs || sign; 
                
                 }
                 if(val == 'start') {
                   sign = this.dateOpt.beforeOnlyDay ?  (this.dateOpt.beforeOnlyDay != i || sign)  : sign ;
                 } 
                  dayArr.push({disabled: this.showBeforeDisabled ? !sign : sign, val: i});
                
           }

        this[val + 'Days'] = dayArr;
        return dayArr
      },
      getDays: function (val, index) {
          this[val + 'Index'] = index;
              this.showCalendars = false;
              this.flag = true;

          this[val + 'Month'] = !this.dateOpt.onlyMonth ?  this[val + 'Month'] : this[val + 'Months'][index].val - 1;
          this[val + 'Day'] = this.dateOpt.onlyMonth ? '' : this[val + 'Days'][index].val;
          this[val + 'Date'] = this.getFormatter(this[val + 'Year'],  this[val + 'Month'] + 1 , this[val + 'Day']);
          let Val;
          if (val === 'start') {
              Val = 'end';
          } else {
              Val = 'start';
          }
           this[Val + 'Disable'] = this[val + 'Date'];
          if (!this['show' + Val + 'Index']){
              this[Val + 'Index'] = -1;
              this[Val + 'Last'] = this[Val + 'Date'];
              this[Val + 'Date'] = '';
              this['show' + Val + 'Index'] = true;
          }
          this['show' + val + 'Index'] = true;
          if (this.type !== 'doubleDate') {
              this.getNowDate();
          } else {
              this.getAllDays(Val);
              this.getAllDays(val);
              this.getNowDate();
             //  this.showCalendars = false;
           
          }

      }
  },
  mounted () {
      if (this.type) {
          this.dateOpt.type = this.type
      }
      this.init();
  },
  watch: {
    'startMonth': {
      handler(val, oldVal) {
        if (oldVal !== '') {
       //   console.log(this.startYear)
          this.getChange('start')
        }
      }
    },
    'endMonth': {
      handler(val, oldVal) {
        if (oldVal !== '' && this.type === 'doubleDate') {
        //  console.log(this.startYear)
          this.getChange('end')
        }
      }
    },
    'startYear': {
      handler(val, oldVal) {
        if (oldVal !== '' && val !== '') {
          this.getChange('start')
        }
      }
    },
    'endYear': {
      handler(val, oldVal) {
        if (oldVal !== '' && this.type === 'doubleDate') {
          this.getChange('end')
        }
      }
    },
    'dateOpt.value': {
        handler(val, oldVal) {
            this.init()
        }
    }
  }
}
</script>

<style lang="scss">
    @import "../../assets/main.scss";
    $spanPadding: 5px;
    $calendarWidth: 18em;
    $calendarHeight: 20.5em;
    $doubleCalendarWidth: 18em;
    $theColor: #227fdf;
    .clearElement{
        content:'';
        width:0;
        height:1px;
        clear:both;
        display:block
    }
    .hover-select{
        cursor:pointer;
        background:$theColor;
        color:white;
        outline: none;
    }
    span.disableDay {
        // background: #f7f3f3;
        cursor: default!important;
       // color: #dcd7d7;
       color:darkgrey
    }
    span.disableDay:hover {
        border:1px solid transparent !important;
    }
  .date-picker {
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
      float:left;
      position: relative;
      .date-button {
          float: left;
          text-align: left;
          position: relative;
          border-radius:0.2em;
          border:1.5px solid $theColor;
          line-height:1.2;
      //    background: $theColor;
        background: rgba($theColor, 0.2);
          color:white;
          span{
              position: absolute;
              right: 9px;
              top: 9px
          }
      }
      .icon-date {
          margin-top: 1px !important;
          float: right !important;
          margin-right: 0.2em !important;;
          font-size: 1.3em;
        }

      .doubleCalendar {
          padding:10px;
          width:calc(2 * #{$doubleCalendarWidth} + 4px)!important;
          .calendars{
              float:left;
              width:$doubleCalendarWidth!important;
              min-height: $calendarHeight !important;
          }
          .calendar-footer{
              width: 100%;
              text-align: left;
              padding-top: 10px;
              float: left;
              span{
                  float:left;
                  margin-top:5px;
                  line-height: 1.9em;
                  padding: 0 0.5em
              }
              input.calendar-input{
                float: left;
                width: 11em !important;
                text-align: center;
                background: transparent;
                height: 2.5em !important;
                color: #4C5E76 !important;
                padding: 0.1em 0.9em;
              }
              button{
                  padding: 2px 12px;
                  float: right;
                  margin: 0 4px;
                  outline: none;
                  height: 30px!important;
              }
          }
      }
      .myCalendar {
          color: #4D4D4D;
          font-size:0.9em;
          outline: none;
         // position: absolute;
          position:fixed;
          border: 1px solid #305ea0;
          top: 100%;
          border-radius: 4px;
        //  background: white;
         background: rgba(#305ea0, 1);
         color:white;
          z-index: 11;
          .calendars {
              width:$calendarWidth;
              border:1px solid rgba(255,255,255,0.3);
              span{
                  padding:$spanPadding;
                  float:left;
                  top:0;
                  outline: none;
                  text-align: center;
                  line-height: 1.4;
              }
              .calendar-header{
                  .date-now{
                      width: calc(100% - 7.4em);
                      padding:0.625em;
                  }
                  .date-before{
                      padding:0.625em;
                      float:left;
                      cursor: pointer;
                  }
                  .date-after{
                      float:right;
                      padding:0.625em;
                      cursor: pointer;
                  }
                  .date-before:hover, .date-after:hover{
                      color: $theColor
                  }
                  span:hover{
                      @extend .hover-select
                  }
              }
              .calendar-header:after,
              .calendar-content:after{
                  @extend .clearElement
              }
              .calendar-content {
                  padding:0.625em;
                  span{
                      width:calc((100% - 1.875em) / 7 - 12px);
                      margin: 1px;
                      border:1px solid transparent;
                  }
                  span.daySelect{
                      @extend .hover-select
                  }
                  span.day-span:hover{
                      border:1px solid $theColor;
                      cursor:pointer
                  }
              }
          }
      }
  }
    .date-picker:after,.myCalendar:after,.calendars:after{
        @extend .clearElement
    }
    .customerOne{
      float: none!important;
      display: inline-block;
      top: 0.5em;
      .date-button{
        background: transparent;
        color: #eadb5c;
        border:0;
        margin:0;
        padding:0;
        height: 2em;
        i{
          display: none;
        }
      }
      .date-button:before{
        content: '【'
      }
      .date-button:after{
        content: '】'
      }
      .date-button:hover{
        text-decoration: underline;
      }
      .calendar-content {
        padding:0.625em;
        span{
        //  width:calc((100% - 1.875em) / 5) !important;
        }
      }
      .hasMarginSpan{
        margin: 0 0.5em;
        display: block;
        float:left;
      }
    }
</style>



