            <VuePerfectScrollbar  @mouseover.once="changeSize" class="table-tbody scroll" id="myBody"  :style="{height: 'calc(' + tableOpt.height + 'px - ' + (tableOpt.noFooter ? 0 : (tableOpt.showTotal ? 7 : 5)) + 'em)', width: tableWidth + 'px'}" ref="bodyContent" @ps-scroll-x="handleScroll($event)" >
