                          </template>
                      </td>
                  </template>
              </tr>
              </tbody>
            </table>
              <slot name="other"></slot>
          </VuePerfectScrollbar>
        </div>
          <div v-if="customerH > 0" class="customerDiv" :style="{height: customerH + 'px'}">
            <slot name="customer" ></slot>
          </div>
        <div class="table-footer " >

            <div :class="{'table-overflow': overflow}"  class="total-div" v-if = "tableOpt.showTotal ">
              <table class="table"   :style="{marginLeft: footerStyle, width: offsetWidth + 'px'}" >
                <thead>
                  <tr  class="totalShow" >
                    <td v-if="totalObj.min > 0" :colspan="totalObj.min" :width="totalObj.before" style="text-align:center">总计 </td>
                    <td v-if="amount !== ''" v-for="(row, $index) in totalObj.column" :width="row.col ? row.width : columnStyle[row.field] ? columnStyle[row.field].width : ''"  :colspan="row.col || 1 " :style="{ textAlign: row.textAlign}">{{amount[row.field.replace(/(^\s*)|(\s*$)/g, "")]}}</td>
                    <td v-if="totalObj.max > 0" :colspan="totalObj.max" :width="totalObj.after"></td>
                </tr>
                </thead>
              </table>
            </div>
            <div class="table-bottom"  v-if="!tableOpt.noFooter">
              <span class="showMsg">
                <span v-if="tableOpt.pageList && tableTotal >= tableOpt.pageList[0] " class="span-pageList" @blur="hideSelect('showPageList')" tabindex="1">
                  每页<span @click="showSelect('showPageList')">{{tableOpt.pageSize}}<i><icon name="sort-asc"  scale="1.6"></icon></i></span>
                    <ul v-show="showPageList">
                        <li v-for="(pageList, $pageListIndex) in tableOpt.pageList" @click="changePageList(pageList, $pageListIndex)">
                            {{pageList}}
                        </li>
                    </ul>
                    条数据；
                </span>
                <span class="text-show">共 {{getTotalPage()}} 页；</span>
                <span   class="text-show">显示第 {{offset.start}} 到 {{offset.end}} 条，</span>
                <span   class="text-show">共{{tableTotal}}条数据</span>
               <span v-if="tableOpt.isSetExport  ? false : tableOpt.isExport && tableData.length > 0" class="span-exportList"   @blur="hideSelect('showExportList')" tabindex="1">
                  <span @click="showSelect('showExportList')">
                    <i>
                      <img src="../../assets/img/table/download.png" />
                      <!--<icon name="download"></icon>-->
                    </i>
                  </span>
                    <ul v-show="showExportList">
                        <li v-for="(typeList, $exportTypeIndex) in exportType" @mousedown="exportNow(typeList.type, $exportTypeIndex)">
                            {{typeList.text}}
                        </li>
                    </ul>
                </span>
                <span class="span-exportList" v-if="tableOpt.isSetExport && tableData.length > 0 " tabindex="1">
                  <span @click="showSelect('showExportList')">
                    <i>
                      <img src="../../assets/img/table/download.png"/>
                    </i>
                  </span>
                 <!--  <ul class="inputDiv" v-show="showExportList">
                    <li>
                      第<input type="text" class="exportInput" v-model="pageStartNum"
                                                     maxlength="8"/>
                      到<input type="text" class="exportInput" v-model="pageEndNum" maxlength="8"/>页
                    </li>
                    <li>
                      <button class="button-default"
                              @mousedown="exportNow('customer', 0)">确定</button>
                      <button class="button-default" @mousedown="hideSelect('showExportList')">取消</button>
                    </li>
                   </ul> -->
                </span>
              </span>
              <!--<div class="pagination" v-if="tableTotal > tableOpt.pageSize">-->
              <div class="pagination" v-if="!tableOpt.noPagination">
                <ul class="pagination-list">
                  <!--<li style="padding:9px;margin-top:-2px">每页显示 <input type="text"  v-model="tableOpt.pageSize" @keydown="getCurrentPage($event)"></li>-->
                  <li @click="pageBegin" class="hasBack fist_Page " :class="{'fist_Page_disabled': tableOpt.pageNumber == 1 || getTotalPage() === 0}">
                    <!--<icon name="step-backward" class="icon" scale="1.2"></icon>-->
                   <!--  <img src="../../assets/img/table/first_page_active.png"> -->
                  </li>
                  <li @click="pageBefore" class="hasBack pre_Page " :class="{'pre_Page_disabled': tableOpt.pageNumber == 1 || getTotalPage() === 0}">
                    <!--<icon name="caret-left" class="icon icon-big" style="margin-left:-0.45em;" scale="1.7"></icon>-->
                    <!-- <img src="../../assets/img/table/page_before_active.png"> -->
                  </li>
                  <li style="padding:9px;margin-top:-2px">
                    第<input type="text" v-model="tableOpt.pageNumber"  @keydown="getCurrentPage($event)" :disabled="!(getTotalPage()>1)">页
                  </li>
                  <li @click="pageNext" class="hasBack next_Page " :class="{'next_Page_disabled': getTotalPage() === tableOpt.pageNumber || getTotalPage() === 0}">
                    <!--<icon name="caret-right" class="icon icon-big" style="margin-right:-4px;" scale="1.7"></icon>-->
                   <!--  <img src="../../assets/img/table/next_page_active.png"> -->
                  </li>
                  <li @click="pageEnd" class="hasBack last_page " :class="{'last_page_disabled': getTotalPage() === tableOpt.pageNumber || getTotalPage() === 0}">
                    <!--<icon name="step-forward" class="icon" scale="1.2"></icon>-->
                    <!-- <img src="../../assets/img/table/last_page_active.png"> -->
                  </li>
                  <li v-if="tableOpt.url || tableOpt.hasRefresh" @click="refreshTable(null, 1)" class="hasBack fresh_page" style="margin-right:1px" >
                    <!--<icon name="refresh" class="icon" scale="1.2" style="margin-right:0.45px;"></icon>-->
                   <!--  <img src="../../assets/img/table/fresh_active.png"> -->
                  </li>
                </ul>
              </div>
            </div>

        </div>
        <dialogs ref="alerts" type="alert"></dialogs>
        <dialogs type="customer" v-if="showExportList" ref="downLoadInput" width="24em">
          <div slot="header">
             下载数据
          </div>
           <div slot="content">
              <ul class="dialog-ul" style="padding: 0.5em 0">
                  <li v-for="(item, $index) in fromOpt ">
                    <label class="label-show">{{item.label}} </label>
                    <inputs  ref="passInput" v-if="item.type == 'input'" :option="item.options"  @getInput="getForm"></inputs>
                  </li>
            </ul>
          </div>
          <div slot="footer">
            <button class="button-default" @click="exportNow('customer', 0)">确定</button>
            <button class="button-default cancel-button" @click="hideSelect('showExportList')">取消</button>
          </div>
        </dialogs>
    </div>
</template>

<script>
    import Vue from 'vue'
    import 'vue-awesome/icons/sort-asc'
    import prefectScroll from 'perfect-scrollbar'
    export default{
        data() {
            return {
                copyData: [],
                offsetWidth:'',
                currentTr: '',
                operateConfirms: '',
                tableWidths: '',
                moveSign: false,
                startX: '',
                startWidth: '',
                columnIndex: -1,
                cursors: 'default',
                sign: true,
                amount: '',
                tipMsg: '没有找到匹配数据',
                threadStyle: {},
                footerStyle: {},

                showExportList: false,

                backWidth:'',
                noDataTips: false,
                loadings: false,
                showPageList:false,
                allIndex: [],
                selectIndex: [],
                checked: false,
                selectArr: [],
                lastPage: 0,
                overflow: false,
                offset: {start: 0, end: 0},
                tableData: [],
                tableTotal: 0,
                queryParam: {},
                tableWidth: '',
                tableHeight: '',
                timer: null,
                allData: [],
                readyData: true,
                pageStartNum: 1,
                pageEndNum: 1,
                operateDataArr: [],
                totalObj: '',
                requestUrl: []
            }
        },
        props: {
        	classes: {
        		
        	},
            tableOpt: {
                type: Object,
                'default'() {
                    return {
                        column: [
                            {field: 'a', title: '测试项目1', width: '100', sortable: false, textAlign: 'center'},
                            {
                                field: 'b',
                                title: '测试项目2',
                                width: '100',
                                sortable: false,
                                textAlign: 'center',
                                type: 'string',
                                formatter: function (item, index) {
                                    return item
                                   // return '<button class="btn btn-info" >'+ item + '</button>'
                                }
                            },
                            {field: 'c', title: '测试项目3', width: '100', sortable: false, textAlign: 'center'}
                        ],
                        pageSize: 5,
                        pageNumber: 1,
                        pageList: [10, 20, 30, 'All'],
                        sortName: 'a',
                        sortOrder: 'asc',
                        url: '',
                        rowName: '',
                      data: [
                        {a: 1, b: 2, c: 'a3'},
                        {a: 12, b: 21, c: 53},
                        {a: 13, b: 22, c: 43},
                        {a: 11, b: 23, c: 33},
                        {a: 13, b: '2e', c: 13},
                        {a: 11, b: 26, c: 3},
                        {a: 1, b: 2, c: 'a3'},
                        {a: 12, b: 21, c: 53},
                        {a: 13, b: 22, c: 43},
                        {a: 11, b: 23, c: 33},
                        {a: 13, b: '2e', c: 13},
                        {a: 11, b: 26, c: 3}
                      ],
                        height: 500,
                        events: {
                            rowClick: function (items, index) {
                            },
                            rowDbClick: function (items, index) {
                            }
                        }
                    }
                }
            },
            customerH: {
                type: Number,
                'default': 0
            }
        },
        methods: {
            getForm: function (val) {
               this.getOption(val, this)
            },
            changeSize: function () {
              // console.log(1)
            },
            endExpand: function (event) {
                if(event.releaseCapture) {
                    event.releaseCapture();
                }
                else if(window.captureEvents) {
                    window.captureEvents(Event.MOUSEMOVE|Event.MOUSEUP);
                }
                this.currentTr.cursor = 'default!important';
                this.startX = '';
                this.startWidth = '';
                document.onmousemove=null;
                document.onmouseup=null;
            },
            getMoveWidth: function (column, index, event) {
                let events  = event || window.event;
                let  plus = events.x - this.startX;
                let widths = parseInt(this.currentTr.parentNode.width || this.currentTr.parentNode.clientWidth);
                if (widths + plus > 0) {
                    this.currentTr.cursor = 'e-resize!important';
                    if (widths + plus > 40)  {
                        this.tableOpt.column[index].width = this.currentTr.parentNode.width = widths + plus;
                        if (this.tableOpt.column[index] && !this.tableOpt.column[index].width) {
                            Vue.set(this.tableOpt.column[index], 'width', (widths + plus));
                        } else {
                            this.tableOpt.column[index].width = this.currentTr.parentNode.width = widths + plus;
                        }
                        this.tableWidths = this.$el.offsetWidth + plus;
                        this.startX = events.x;
                        this.readyData = true;
                        this.getColSpan();
                        this.resizeTable();
                        // this.colSpanBefore();
                        // this.colSpanAfter()
                    }
                }
            },
            changeCursor: function (event) {
            },
            getStartX: function (column,index, event) {
                this.currentTr = event.target;
                this.startX = event.x;
                this.startWidth = event.target.parentNode.offsetWidth;
                if (event.setCapture)
                    event.setCapture();
                else if (window.captureEvents)
                    window.captureEvents(Event.MOUSEMOVE | Event.MOUSEUP);
                let that = this;
                document.onmousemove = function (ev) {
                    that.getMoveWidth(column, index, ev)
                };
                document.onmouseup = function (ev) {
                    that.endExpand(event)
                }

            },
            getColSpanHead: function(field){
              if(this.tableOpt.totalhead && this.tableOpt.totalhead.length > 0){
                for (let i = 0; i < this.tableOpt.totalhead.length; i++){
                  for (let j = 0; j < this.tableOpt.totalhead[i].totalFieldield.length; j++){
                    if(field === this.tableOpt.totalhead[i].totalFieldield[j]){
                      return 1;
                    }
                  }
                }
                return 2;
              }
              return 1;
            },
            getColSpan: function () {
                let number = [];
                let arr = [];
                let totalColumn = [];
                for (let i=0; i<this.tableOpt.column.length; i++) {
                    for (let j=0; j< this.tableOpt.totalOpt.length; j++) {
                         if (this.tableOpt.column[i].field === this.tableOpt.totalOpt[j].field) {
                             number.push(i);
                             totalColumn.push({index: i, obj: this.tableOpt.totalOpt[j]});
                         }
                    }
                }
                totalColumn.sort(this.sortColumnIndex);
                for(let i=0;i < totalColumn.length; i++) {
                  arr.push(totalColumn[i].obj);
                  if((i + 1) < (totalColumn.length -1)) {
                     let len = totalColumn[i+1].index  -  totalColumn[i].index;
                     if(len > 1) {
                         let w = 0;
                         for(let j = totalColumn[i].index + 1; j < totalColumn[i+1].index; j++ ) {
                            w += parseFloat(this.columnStyle[this.tableOpt.column[j].field].width);
                         }
                         arr.push({title: '', field: '', textAlign: 'center', width: w + 10 * (len - 2), col: len - 1});
                     }
                   }
                }
                let min = Math.min.apply(null, number);
                let max = Math.max.apply(null, number);
                this.totalObj = {
                    min: min,
                    max: this.tableOpt.column.length - max - 1,
                    column: arr,
                    before: this.colSpanBefore(min),
                    after: this.colSpanAfter(min)
                  }

            },
            sortColumnIndex: function (x,y) {
               if (x.index < y.index) {
                     return -1;
                  } else if (x.index > y.index) {
                     return 1;
                  } else {
                     return 0;
                  }
            },
	      exportNow: function (type, index) {
	        let myAlert = this.$refs.alerts;
	        if (type == 'all') {

	          this.downloadData({
	            pageSize: this.tableTotal,
	            pageNumber: 1
	          })

	        } else if (type == 'customer') {
	          if (this.pageStartNum > this.pageEndNum) {
	            myAlert.show({
	              msg: '终止页不能小于起始页'
	            })
	          } else if (this.pageStartNum == 0 || this.pageEndNum == 0) {
	            myAlert.show({
	              msg: '起始页和终止页都不能为0'
	            })
	          } else {
	            let endNm = ((this.pageEndNum - this.pageStartNum) + 1) * this.tableOpt.pageSize;
	            if (this.pageStartNum > this.getTotalPage() || this.pageEndNum > this.getTotalPage()) {
	               myAlert.show({
	                msg: '起始页和终止页都不能大于总页数'
	              })
	            } else if (endNm > 10000) {
	              myAlert.show({
	                msg: '下载条数不能超过10000'
	              })
	            }  else {
	              this.downloadData({
	                pageSize: this.tableOpt.pageSize,
	                pageNumber: this.pageStartNum,
	                pageEndNum: this.pageEndNum
	              });
	              this.showExportList = false
	            }
	          }
	        }
	        else {
	          this.downloadData({
	            pageSize: this.tableOpt.pageSize,
	            pageNumber: this.tableOpt.pageNumber
	          })
	        }
	      },
            refreshTable: function (queryParams, pageNumber) {
                this.amount = '';
                if (queryParams !== null) {
                    this.tableOpt.queryParam = queryParams;
                }
                this.tableOpt.pageNumber = pageNumber || this.tableOpt.pageNumber;
                this.changeData();
                this.backWidth = this.$el.offsetWidth;
                if(this.tableOpt.url === '' || typeof this.tableOpt.url === 'undefined') {
                   this.$emit('freshStaticTable');
                }
                //this.onResize(true);
            },
            hideSelect: function (val) {
                this[val] = false;
                if(this.tableOpt.isSetExport) {
                  this.$refs.downLoadInput.close();
                  this.pageStartNum = this.pageEndNum = 1;
                }
            },
            changePageList: function (item, index) {
                if (item === 'All') {
                  this.tableOpt.pageSize = this.tableTotal
                } else {
                  this.tableOpt.pageSize = item;
                }
                this.showPageList = this.showPageList === false;
            },
            showSelect: function (val) {
                this[val] = !this[val];

                if(this.tableOpt.isSetExport) {
                  let that = this;
                  that.$nextTick(function () {
                     that.$refs.downLoadInput.show();
                  })
                }
            },
            setColumnStyle: function (val) {
                let arr = {};
                let width = '';
                if (typeof val.width !== 'undefined') {
                    if ((/^[\w\W]*[%]+[\w\W]*$/).test(val.width)) {
                        width = val.width;
                    } else {
                        width = val.width;
                    }
                    arr['width'] = width;
                }
                arr['textAlign'] = val.textAlign;
                this.columnStyle[val.field] = arr;
            },
            getSort: function (val, order) {
                this.tableOpt.sortName = val.field;
                this.tableOpt.sortOrder = order;
                this.changeData()
            },
            downloadData: function (param) {
                let params = {};

                for (let item in this.tableOpt.queryParam) {
                    params[item] = this.tableOpt.queryParam[item];
                }
                params['pageSize'] = param.pageSize;
                params['pageNumber'] = param.pageNumber;
                if(typeof params.pageSize === 'undefined' || params.pageSize === '') {
                  delete  params.pageSize;
                }
                if(typeof params.pageNumber === 'undefined' || params.pageNumber === '') {
                  delete  params.pageNumber;
                }
                if(typeof params.sortOrder === 'undefined' || params.sortOrder === '') {
                  delete  params.sortOrder;
                }
                if(typeof params.sortName === 'undefined' || params.sortName === '') {
                  delete  params.sortName;
                }
              //  if (!(this.tableOpt.data && this.tableOpt.data.length > 0)) {

              //   params['sortName'] = this.tableOpt.sortName;
              //    params['sortOrder'] = this.tableOpt.sortOrder;
              //  }

	              if (this.tableOpt.isSetExport) {
	                 params.pageEndNum = param.pageEndNum;
	               }
                this.downLoad({
                  url: this.tableOpt.exportURL,
                  data: params
                })
                // this.getRequest({
                //    url: this.tableOpt.exportURL,
                //    param: params,
                //    method: 'post',
                //    success: function (data) {

                //    }
                // })
            },
            getOverflow: function () {
                let rowHeight = 40;
                let dataHeight =0;
                let that = this;
                that.$nextTick(function () {
                  if (that.$refs.rowHeight && that.$refs.rowHeight[0] && that.$refs.rowHeight[0].offsetHeight) {
                     rowHeight = that.$refs.rowHeight[0].offsetHeight + 1.5;
                  }

                  dataHeight = that.tableData.length * rowHeight;
                  that.overflow = (dataHeight > (that.tableOpt.height - that.customerH));
                  that.tableHeight = rowHeight * (that.offset.end - that.offset.start + 1) + 'px';
                  if (that.$refs.bodyContent && that.$refs.bodyContent.offsetWidth !== 0){
                      that.backWidth = that.$refs.bodyContent.offsetWidth
                  }
                  if(that.$refs.tableBody) {
                      that.offsetWidth = that.$refs.tableBody.clientWidth ;
                   }
                })
            },
            gerRowHeight: function () {
              let rowHeight = 40;
              let that = this;
              if (that.$refs.rowHeight && that.$refs.rowHeight[0] && that.$refs.rowHeight[0].offsetHeight) {
                     rowHeight = that.$refs.rowHeight[0].offsetHeight + 1.5;
              }
              return rowHeight
            },
            getOffset: function () {
                if (this.tableOpt.pageNumber === 1 && this.tableTotal > 0) {
                    this.offset.start = 1
                } else if(this.tableTotal > 0){
                    this.offset.start = this.tableOpt.pageSize * (this.tableOpt.pageNumber - 1) + 1;
                }
                let end = this.tableOpt.pageSize === 'all' ? this.tableTotal : this.offset.start + this.tableOpt.pageSize - 1;
                if (end > this.tableTotal) {
                    this.offset.end = this.tableTotal;
                } else {
                    this.offset.end = end
                }
                this.getTotalPage();
            },
            getTotalPage: function () {
                this.lastPage = this.tableOpt.pageSize === 'all' ? 1 : Math.ceil(this.tableTotal / this.tableOpt.pageSize);
                return this.lastPage
            },
            getCurrentPage: function (event) {
                this.sign = true;
                if (event.keyCode === 13) {
                  this.changeNumber()
               }
            },
            changeNumber: function () {
              let val = this.tableOpt.pageNumber;
              if (val !== '' && this.sign  && this.lastPage > 0 && this.tableData.length > 0) {
                if (0 < parseInt(val.toString()) && parseInt(val.toString()) <= this.getTotalPage()) {
                  this.getOffset();
                  this.changeData();
                } else  if (val === 'undefined') {
                 // this.tableOpt.pageNumber = 1;
                } else  {
                  this.$refs.alerts.show({msg: '请输入正确的页数'});
                  this.tableOpt.pageNumber = 1;
                  this.getOffset();
                  this.changeData();
                }
              }
            },
            pageBegin: function () {
                this.sign = true;
                if(this.tableOpt.pageNumber !== 1) {
                    this.tableOpt.pageNumber = 1;
                    this.changeNumber();
                }
            },
            pageEnd: function () {
                this.sign = true;
                if(!(this.tableOpt.pageNumber == this.lastPage && this.lastPage > 1 || this.lastPage == 1)) {
                    this.tableOpt.pageNumber = this.lastPage === 0 ? 1: this.lastPage;
                    this.changeNumber();
                }
            },
            pageNext: function () {
                this.sign = true;
              if(this.getTotalPage() === this.tableOpt['pageNumber'] || this.getTotalPage() === 0) {
                    //this.pageBegin()
                } else {

                    this.tableOpt['pageNumber']++;
                    this.changeNumber();
                }
            },
            getParams: function () {
                if (this.tableOpt.queryParam) {
                    this.queryParam = this.tableOpt.queryParam;
                }
              //  if (this.tableOpt.queryType != 'form') {
                    this.queryParam['pageSize'] = this.tableOpt.pageSize;
                    this.queryParam['pageNumber'] = this.tableOpt.pageNumber;
                    if(this.tableOpt.sortName) {
                       this.queryParam['sortName'] = this.tableOpt.sortName;
                    }
                    if(this.tableOpt.sortOrder) {
                      this.queryParam['sortOrder'] = this.tableOpt.sortOrder;
                    }

                //}
                return this.queryParam
            },
            //flag: 是否清空初始数据
            createData: function (flag) {
                let that = this;
                if(!flag) {
                   that.allData = [];
                   that.copyData = [];
                   that.operateDataArr = [];
                }
             
                if(!flag ) {
                   //that.offset.start = that.offset.end = that.tableTotal = 0;
                }

                that.tableData = [];
                that.noDataTips = false;
                let params = that.getParams();
                if (this.backWidth !== 0) {
                    that.loadings = true;
                }
                if (that.tableOpt.url && that.tableOpt.url !== '') {
                    that.allData = [];
                    that.copyData = [];
                    that.tableData = [];
                    that.amount = '';
                    that.requestUrl.push(that.tableOpt.url);
                    that.getRequest({
                        url: that.tableOpt.url,
                        method:  that.tableOpt.method ||  'get',
                        param: params,
                        success: function (data, request) {

                             if(that.tableOpt.shareTable) {
                               let url = request.responseURL.split("/");
                               url = url[url.length - 2] + '/' + url[url.length - 1];
                               if(that.requestUrl[that.requestUrl.length -1] != url){
                                   return;
                               }
                               that.requestUrl = [];
                             }
                            let rows = that.tableOpt.rowName || 'data';
                            let totals = that.tableOpt.totalName || 'total';
                            let dataRow  =that.tableOpt.dataRow || '';
                            let amount = dataRow !== '' ? data[rows]['statistics'] : '';
                            let datas = dataRow === '' ? data[rows] : data[rows][dataRow];
                          
                            if (((that.tableOpt.method === 'get' && that.tableOpt.url.indexOf('.json') > -1) || that.tableOpt.pageSize === 'all') && data.code === 1 && (datas.length > 0) && data[totals] > 0) {

                                that.noDataTips = false;
                                that.tableData = [];
                                if(!flag) {
                                  that.allData  = datas;
                                  for(let m =0; m<datas.length; m++) {
                                      that.copyData.push(datas[m])
                                  }
                                }
                                that.amount = amount;
                                that.tableTotal = that.allData.length;
                                let length = that.tableTotal > that.tableOpt.pageNumber * that.tableOpt.pageSize  ? that.tableOpt.pageNumber  * that.tableOpt.pageSize : that.tableTotal;
                                if (that.tableOpt.pageSize === 'all') {
                                    that.tableData = dataRow === '' ? data[rows][dataRow] : data[rows];
                                } else {
                                    for (let i= (that.tableOpt.pageNumber - 1) * that.tableOpt.pageSize ; i < length; i++) {
                                        that.tableData.push(that.allData[i]);
                                    }
                                }
                                that.loadings = false;
                                that.getOffset();

                            }  else if ((data[totals] !== '0' || data[totals] !== 0 ) && (data[rows] && (datas.length > 0)) && that.tableOpt.pageSize !== 'all'){
                                that.noDataTips = false;
                                that.amount = amount;
                                that.tableTotal = data[totals] || data[rows].length;
                                that.tableData   =  datas;
                                that.loadings = false;
                                that.getOffset();
                            }  else {
                                that.tableTotal = 0;
                                that.offset = {start: 0, end: 0};
                                that.tableData = [];
                                that.noDataTips = true;
                                that.loadings = false;
                                that.tableData = []
                            }
                            that.$emit('getTotals',  that.tableTotal);
                            let returnData = {};
                            for(let key in data) {
                              if(key !== 'code' && key !== 'message' && key !== 'data') {
                                returnData[key] = data[key];
                               }
                            }

                            that.$emit('getOtherData', returnData);
                            that.$nextTick(function () {
                                that.getOverflow();
                            });
                        },
                        error: function (){
                            that.tableTotal = 0;
                            that.offset = {start: 0, end: 0};
                            that.tableData = [];
                            that.noDataTips = true;
                            that.loadings = false;
                            let returnData = {};
                            for(let key in data) {
                              if(key !== 'code' && key !== 'message' && key !== 'data') {

                                returnData[key] = data[key];
                              }
                            }
                            that.$emit('getOtherData', returnData)
                            that.$emit('getTotals',  that.tableTotal);
                        }
                    }, that.tableOpt.queryType === 'form');
                } else if (that.tableOpt.data && that.tableOpt.data.length > 0){
                    that.noDataTips = false;
                    that.tableData = [];
                    if(!flag) {
                       that.allData =  that.tableOpt.data;
                       for(let m =0; m< that.tableOpt.data.length; m++) {
                          that.copyData.push(that.tableOpt.data[m])
                       }
                    }
                    that.tableTotal = that.allData.length;
                    let length = that.tableTotal > that.tableOpt.pageNumber  * that.tableOpt.pageSize  ? that.tableOpt.pageNumber  * that.tableOpt.pageSize : that.tableTotal;
                    for (let i= (that.tableOpt.pageNumber - 1) * that.tableOpt.pageSize ; i < length; i++) {
                        that.tableData.push(that.allData[i]);
                    }
                    that.getOffset();
                    that.loadings = false;
                    that.$nextTick(function () {
                        that.getOverflow();
                    });

                } else {
                    that.tableData = [];
                    that.offset.start =  that.offset.end = 0;
                    that.lastPage = 0;
                    that.tableTotal = 0;
                    that.noDataTips = true;
                    that.loadings = false;
                }
                this.threadStyle = 0;
                this.onResize();

            },
            changeData: function (flag) {
                if(typeof flag  === 'undefined') {
                  this.createData();
                } else {
                   this.createData(flag);
                }
                this.checked = true;
                this.selectIndex = [];
                this.selectArr = [];
                this.getAllSelect();
            },
            pageBefore: function () {
                this.sign = true;
                if (this.tableOpt.pageNumber === 1) {
                    //this.pageEnd()
                } else {
                    this.tableOpt.pageNumber --;
                    this.changeNumber();
                }
            },
            defaultSet: function (val, fun) {
                if (typeof val ===  'undefined') {
                    return '';
                }
               return val[fun]
            },
            getFormatter: function (val, index, row, fun, numIndex, showText) {

                let obj = fun(val, index, row, this,numIndex, this.offset.start);
                if (typeof obj === 'object') {
                    Vue.component('components', {
                        template: obj.template,
                        methods: obj.methods || {},
                        props: ['item', 'index']
                    });
                } else if(showText) {
                    if(obj) {
                       return obj.toString().replace(/<\/?.+?>/g,"")
                    }
                } else {
                   return obj
                }
            },
            getEvents: function (val, index, fun, event) {
               // console.log(event.target.type)
               if (event.target.type !== 'checkbox' && event.target.type !== 'radio') {
                 let exp = /^((?!Db).)*$/;
                 let that = this;
                 if (exp.test(fun)) {
                   clearTimeout(that.timer);
                   that.timer = setTimeout(function () {
                     that.$emit(fun, {val: val, index: index})
                   }, 300);
                 } else {
                   clearTimeout(that.timer);
                   this.$emit(fun, {val: val, index: index})
                 }
               }

            },
            resetSearchData: function (data) {
                let that = this;
                that.offset.start = that.offset.end = that.tableTotal = 0;
                that.loadings = true;
                that.noDataTips = false;
                that.tableData = [];
                that.allData = data;
                that.tableTotal = that.allData.length;
                let length = that.tableTotal > that.tableOpt.pageNumber  * that.tableOpt.pageSize  ? that.tableOpt.pageNumber  * that.tableOpt.pageSize : that.tableTotal;
                for (let i= (that.tableOpt.pageNumber - 1) * that.tableOpt.pageSize ; i < length; i++) {
                     that.tableData.push(that.allData[i]);
                 }
                that.getOffset();
                that.loadings = false;
                that.$nextTick(function () {
                     that.getOverflow();
                });
            },
            searchTable: function (param) {
               let data = [];
               if(param) {
                 for(let i=0; i<this.copyData.length; i++) {
                  let current = this.copyData[i];
                  let flag = true;
                  for(let key in param) {
                     if(current[key].toString().indexOf(param[key]) > -1) {
                       flag = flag && true;
                     } else {
                       flag = false;
                       break
                     }
                  }
                  if(flag) {
                    data.push(current)
                  }
                }
               } else {
                 for(let i=0; i<this.copyData.length; i++) {
                    let current = this.copyData[i];
                    data.push(current)
                 }
               }
               this.resetSearchData(data)
            },
            removeRepeat: function (arr, val) {
                for (let i = 0; i < arr.length; i++) {
                  if (arr[i] === val) {
                      return {index: i, show: true}
                  }
              }
              return {index: '', show: false}
            },
            getCheckSelect: function (item, index){
               if (!this.removeRepeat(this.selectIndex, index).show) {
                   this.selectIndex.push(index);
                   this.selectArr.push(item);
               } else {
                   let rowId = this.removeRepeat(this.selectIndex, index).index;
                   this.selectIndex.splice(rowId, 1);
                   this.selectArr.splice(rowId, 1);
                   if (this.selectIndex.length === 0) {
                       this.checked = false
                   }
                  }
               if (this.selectArr.length === this.tableData.length) {
                   this.getAllSelect();
               }
               return this.selectArr
            },
            getRadioSelect: function (item, index){
                this.selectIndex = [];
                this.selectIndex.push(index);
                let arr = [];
                arr.push(item);
                this.selectArr = arr;
                return item
            },
            getAllSelect: function () {

                let arr = [];
                this.allIndex = [];
                for (let i = 0; i < this.tableData.length; i++) {
                    this.allIndex.push(i);
                    arr.push(this.tableData[i])
                }
                this.checked === true ? (this.checked  = false, this.selectArr = [], this.selectIndex = [])
                    : (this.checked  = true, this.selectArr = arr, this.selectIndex = this.allIndex);
                return this.selectArr
            },
            //重置表格对象
            resetTableOpt: function (tableOpt, flag) {
             
                this.sign = false;
                for(let item in tableOpt){
                    this.tableOpt[item] = tableOpt[item]
                }
                this.readyData = true;
                this.tableOpt.pageNumber = 1;
	              if (tableOpt.isLoadData != false) {
                   if(typeof flag !== 'undefined') {
                     this.changeData(flag);
                   } else {
                     this.changeData();
                   }

	              }
                this.handleScroll();

            },
            resizeTable: function (width, height) {
                let that = this;
                that.tableWidth =  that.tableWidths = that.backWidth = width ;
                that.backWidth = that.$el.offsetWidth;

                that.tableOpt.height =  height - that.customerH;
                that.getOverflow();

                //that.handleScroll();
            },
            getSelected: function () {
                return this.selectArr
            },
            getSelectedIndex: function () {
                return this.selectIndex
            },
            updateRow: function (index, row, keys) {
            	let inx;
            	if(index===null || index === '') {
            		for(let i=0, l=this.tableData.length; i<l; i++) {
            			let items = this.tableData[i];
            			let flag = keys.length > 0 ? true : false;
            			for(let j=0;j<keys.length;j++) {
            				flag = flag && items[keys[j]] === row[keys[j]];
            			}
            			if(flag) {
            				inx = i;
            				break;
            			}
            			break
            		}
            	} else {
            		inx = index;
            	}
                let rows = this.tableData[inx];
                 if(typeof rows !== 'undefined' && typeof row !== 'undefined') {
                   for(let item in row){
                     rows[item] = row[item];
                   }
                   this.tableData[inx] = rows
                }
            },
            handleScroll: function (event) {
                let target = event ? event.target : this.$refs.bodyContent;
                let scroll = target.scrollLeft;
                this.footerStyle = this.threadStyle = '-' + scroll + 'px';

            },
            colSpanBefore: function (min) {
               let minWidth = 0;
               let flag = false;
               for (let m = 0; m < min; m++) {
                  let width = this.columnStyle[this.tableOpt.column[m].field].width;
                  if (width && (width.toString().indexOf('%') > -1)) {
                    minWidth = minWidth +  parseFloat(width.replace('%',''));
                    flag = true
                  } else {
                     minWidth = minWidth + parseFloat(this.columnStyle[this.tableOpt.column[m].field].width) + 10 ;
                  }
               }

               return flag ? (minWidth + '%') : (minWidth - 10)
           },
           colSpanAfter: function (min) {
               let maxWidth = 0;
               let flag = false;
               let start = min + this.tableOpt.totalOpt.length;
               for (let n = start; n < this.tableOpt.column.length; n++) {
                  let width = this.columnStyle[this.tableOpt.column[n].field].width;
                  if (width && (width.toString().indexOf('%') > -1)) {
                    maxWidth = maxWidth +  parseFloat(width.replace('%',''));
                    flag = true
                  } else {
                    maxWidth = maxWidth + parseFloat(width) + 10
                  }
                }

               return flag ? (maxWidth + '%') : (maxWidth - 10)
           },
            //获取所有的数据，，只针对客户端，服务端请直接向数据库请求数据,
            getAllData: function () {
                return this.allData;
            },
           //获取所有的数据，，只针对客户端，返回所有数据,不管当前的查询条件
            getDealData: function () {
                return this.copyData;
            },
            //flag:0(添加)，1()
            addOpeFlag: function (row, flag) {
              let item = {};
              for(let key in row) {
                 item[key] = row[key];
              }
              item['flag'] = flag;
              this.refreshOperateDataArr(item);
            },
            refreshOperateDataArr: function (item) {
              let len = this.operateDataArr.length;
               if(len == 0) {
                 this.operateDataArr.push(item);
               } else {
                for(let i = 0; i<len; i++) {
                  let curr = this.operateDataArr[i];
                  let flag = true;
                  for(let key in item) {
                      if(key != 'flag') {
                         if(curr[key] == item[key]) {
                            flag =  flag && true
                         } else {
                            flag = false;
                            break;
                          }
                      }
                  }
                  if(flag) {
                     this.operateDataArr.splice(i,1);
                     return
                  }
                  if(!flag && i === len - 1) {
                    this.operateDataArr.push(item);
                    return
                  }
                }
              }

            },
            getOperateData: function () {
               return this.operateDataArr;
            },
           //添加删除行数据（多条或者一条，删除重复，最新添加的放最上面，此时的pageNumber应当是1， pageSize应当为all或者直接忽略，总数在上升）
            //data：要添加的数据（数组）， key: 去重字段，如果是字符串，只有一个主键，如果有多个主键，用数组, type:1删除，0：添加, hasSearch:是否有查询条件
            //flags: true(将操作的那一行数据复制到operateDataArr里面，通常编辑列表会用到改属性，一般情况下不做操作)
            operateData: function (data, key, type, hasSearch, flags) {

                 let that = this;
                 let Len = this.allData.length;
                 let oldData = [];
                 for(let v = 0; v < Len; v++) {
                    oldData.push(this.allData[v]);
                 }
                 let newData = data;
                 let newLen = newData.length;
                 let oldLen = oldData.length;
                if (oldLen === 0) {
                	for (let m=0; m<newLen; m++){
                	   	this.allData.push(newData[m]);
                      if(flags) {
                        this.addOpeFlag(newData[m], type == 1 ? 0 : 1);
                       }
                	  }
                  this.copyData = this.allData;
                  this.resetTableOpt({
                     data: that.allData
                  }, flags ? true : false)

                } else {

                    for (let i=0; i<newLen; i++) {

                        for (let j = 0  ; j < this.allData.length; j++) {

                            let flag = true;
                            if (typeof key === 'string') {
                                  flag = flag &&  this.allData[j][key] === newData[i][key];
                            } else {
                                for(let m=0; m<key.length; m++) {
                                      flag = flag && ( this.allData[j][key[m]] == newData[i][key[m]])
                                }
                            }

                            if (flag) {
                                    if(flags) {
                                       that.addOpeFlag(that.allData[j],type == 1 ? 0 : 1);
                                    }
                                    that.allData.splice(j,1);
                                    if(!hasSearch) {
                                        that.copyData = that.allData;
                                    }
                                    if(type === 0) {
                                      if(newLen === 1) {
                                        that.$refs.alerts.show({
                                          msg: '表格中已存在该条数据，即将替换'
                                        });
                                      }
                                      break;
                                    } else {
                                      that.tableTotal = that.allData.length;
                                      that.resetTableOpt({
                                          data: that.allData
                                      }, flags ? true : false)
                                      if(hasSearch) {
                                         that.getNewCopy(data, key, type);
                                       }
                                      return
                                    }
                            }
                        }
                    }
                     if(type === 0) {
                           for (let t=0; t<newLen; t++){
                              that.allData.unshift(newData[t]);
                              if(flags) {
                                 that.addOpeFlag(newData[t],type == 1 ? 0 : 1);
                              }
                           }
                           that.tableTotal = that.allData.length;
                           that.copyData = that.allData;
                           that.resetTableOpt({
                              data: that.allData
                           }, flags ? true : false)
                          // that.resetSearchData(that.allData);
                    }
                }
            },
            getNewCopy: function (data, key, type) {
                let that = this;
                for (let i=0; i<data.length; i++) {
                  for (let j = 0; j < that.copyData.length; j++) {

                            let flag = true;
                            if (typeof key === 'string') {
                                  flag = flag &&  that.copyData[j][key] === data[i][key];
                            } else {
                                for(let m=0; m<key.length; m++) {
                                      flag = flag && (that.copyData[j][key[m]] == data[i][key[m]])
                                }
                            }

                            if (flag) {
                                that.copyData.splice(j,1);
                                if(type === 0) {
                                  break;
                                } else {
                                   return
                               }
                            }
                  }
                }
            },

            //清空表格数据  'all':删除所有数据；；；；方法只对客户端读取数据生效
            clearAllData: function (numbers) {
                let pageNumber = numbers || this.tableOpt.pageNumber;
                if (pageNumber !== 'all') {
                    let length = this.tableTotal > (pageNumber + 1) * this.tableOpt.pageSize ? pageNumber  * this.tableOpt.pageSize : this.tableTotal;
                    let deleteStart = (pageNumber - 1) * this.tableOpt.pageSize ;
                    let deleteLen = length - deleteStart;
                     this.allData.splice(deleteStart, deleteLen)
                } else {
                    this.allData = [];
                }
                this.tableTotal = this.allData.length;
                this.resetTableOpt({
                    data: this.allData
                });
            },
            repairOption: function (item, value) {
                if (typeof this.tableOpt[item] === 'undefined') {
                    this.$set(this.tableOpt, item, value);
                }
            },
          //sign代表是否除去筛选框的高度
            onResize: function (sign, parentObj) {
                let that = this;
                let parent = null;

                that.$nextTick(function () {
                  if (parentObj) {
                    parent = parentObj;
                  } else {
                    if (that.$parent.$refs.myOwnTable) {
                      parent = that.$parent.$refs.myOwnTable;
                    }
                  }

                   if(parent) {

                     that.resizeTable(parent.width, parent.clientHeight - that.$refs.myThead.clientHeight);
                   }


                });
            }
        },
        created: function () {
            if (this.tableOpt.column && this.tableOpt.column.length > 0 ) {
                for (let i = 0; i< this.tableOpt.column.length; i++) {
                    let columns = this.tableOpt.column[i].field;
                    if (columns && columns !== '') {
                        this.tableOpt.column[i].field = columns.replace(/(^\s*)|(\s*$)/g, '');
                    }
                }
            }
            if(this.tableOpt.showTotal) {
              this.getColSpan()
            }
            this.repairOption('pageNumber', 1);
            this.repairOption('pageSize', 16);
        },
        mounted: function (){
            this.changeData();
            let that = this;
            this.$nextTick(function () {
                that.tableWidth = that.tableWidth = that.tableOpt.width || '';
                that.backWidth = that.$el.offsetWidth;
                 that.onResize();
            });

        },
        computed: {
           fromOpt: function () {
              return [
                    {
                        type: 'input',
                        label: '起始页码',
                        options: {
                            type: 'normalInput',
                            width: 'calc(100% - 6em)',
                            key: 'pageStartNum',
                            placeHolder: '',
                            limit: 'number',
                            length: 6,
                            value: this.pageStartNum
                        }
                    },
                    {
                        type: 'input',
                        label: '终止页码',
                        options: {
                            type: 'normalInput',
                            width: 'calc(100% - 6em)',
                            key: 'pageEndNum',
                            placeHolder: '',
                            length: 8,
                            limit: 'number',
                            value: this.pageEndNum
                        }
                    }
                ]
           },
           exportType: function () {
              let arr = [{text: '当前页', type: 'current'}, {text: '全部数据', type: 'all'}];
              if(this.tableOpt.noExportCurrent) {
                 arr = [{text: '全部数据', type: 'all'}]
              }
              return  arr
            },
           columnStyle: function () {
                let styles = {};
                if (this.readyData) {
                    this.readyData = false;

                    for (let i=0; i<this.tableOpt.column.length; i++) {
                        let arr = {};
                        let width = '';
                        let val = this.tableOpt.column[i];
                        if (typeof val.width !== 'undefined') {
                            if ((/^[\w\W]*[%]+[\w\W]*$/).test(val.width)) {
                                width = val.width;
                            } else {
                                width = val.width;
                            }
                            arr['width'] = width;
                        }
                        arr['textAlign'] = val.textAlign;
                        styles[val.field] = arr;
                    }
                    return styles
                } else {
              
                   return styles
                }

            },
           diffthead: function () {
              var column = this.tableOpt.column;
              var headArr = this.tableOpt.totalhead;
              var arrhead = [];
              var arrheadbom = [];
              var colums = [];
              headArr.forEach(function (item) {
                arrhead.push(...item.totalFieldield);
              })
             var flag = true, index = 0, colspan = 0;
              for(let i = column.length-1; i >= 0 ; i--){
                if(arrhead.indexOf(column[i].field) === -1){
                  colums.unshift(column[i])
                }else{
                  if(flag){
                    for (let j = 0; j < headArr.length; j++){
                      if(headArr[j].field === column[i].field){
                        colums.unshift(headArr[j]);
                        index = headArr[j].totalFieldield.length;
                        flag = false;
                        colspan = 0;
                        break;
                      }
                    }
                  }
                  colspan++;
                  column[i].borderTop = "none";
                  if(colspan === index){
                    flag = true;
                    if(navigator.userAgent.indexOf("Chrome")>-1){
                      column[i].borderTop = "1px solid rgba(255, 255, 255, 0.15)";
                    }
                  }
                  if(navigator.userAgent.indexOf("Chrome") === -1){
                    column[i].borderTop = "1px solid rgba(255, 255, 255, 0.15)";
                  }
                  arrheadbom.unshift(column[i])
                }
              }
              return {
                column:colums,
                arrheadbom:arrheadbom
              };
           }
        },
        watch: {
            'tableOpt.pageNumber': {
                handler(val, oldVal) {
                },
                deep: true
            },
            'tableOpt.pageSize': {
                handler(val, oldVal) {
                    if (this.tableData.length > 0 &&  val !== '') {
                        this.tableOpt.pageNumber = 1;
                        this.changeData();
                        this.getOffset()
                    }
                },
                deep: true
            },
	    'pageEndNum': {
	        handler(val, oldVal) {
	          if (val !== '') {
	                  this.pageEndNum = val.toString().replace(/[^\d]/gi, '');
	          }
	        }
	    },
	    'pageStartNum': {
	        handler(val, oldVal) {
	          if (val !== '') {
	                 this.pageStartNum = val.toString().replace(/[^\d]/gi, '');
	          }
	        }
	    },
            'moveSign': {
                handler(val, oldVal) {
                    if (val && this.tableOpt.totalOpt && this.tableOpt.totalOpt.length > 0) {

                        this.colSpanBefore();
                        this.colSpanAfter();
                    }
                }
            }
        }
    }
</script>

