    Vue.prototype.downLoad = function (params) {
      /*
       params.url:下载接口；
       params.data: 传递的参数；格式：{something: something}
       */
      var form = document.createElement('form');
      form.setAttribute('name', 'forms');
      form.setAttribute('target', '#myForms');
      form.setAttribute('id', 'forms');
      form.setAttribute('method', 'post');
      document.body.appendChild(form);
      for (var item in params.data) {
        var newElement = document.createElement("input");
        newElement.setAttribute('name', item);
        newElement.setAttribute('type', 'hidden');
        newElement.setAttribute("value", params.data[item]);
        form.appendChild(newElement);
      }
      // console.log(params.data)
      form.setAttribute('action', this.getPath(params.url));
      form.submit();
      document.body.removeChild(form);
    };

